import React from 'react';


function TargetQuan_1() {
	return (
		<>
			<div className="px-4" style={{marginTop:30}}>
					<MainContents />
			</div>
		</>
	);
};
export default TargetQuan_1
function MainContents() {
	const title = 'Chapter 1 Installing TargetQuan';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			<h2>1. Installing TargetQuan</h2>
			<hr className='class-1'/>
			<h2>2. System Requirements </h2>
				<p>
					To run TargetQuan, you need to meet the minimum requirements for  hardware and software. 
				</p>
				<p>
					An Internet connection is required for automatic license checking. 
					If the  computer where you want to use TargetQuan has no Internet  connection, 
					please have the following available:  
				</p>
				<ul>
				<li>You need (temporary) access to another computer with a working  
				connection to the Internet. TargetQuan 3.2 is not installed on this  computer! </li>
				<li>You have prepared an USB stick with sufficient free memory.</li>
				</ul>

				 <hr className='class-2'/>
				<h4>2.1 Hardware Requirements</h4>
				<p>
					The minimum computer requirements for installation of TargetQuan  are as follows: 
				</p>
				<ul>
					<li>Intel™ Pentium™ 4 Processor, 1 GHz or faster </li>
					<li>1 GB RAM</li>
					<li>20 GB of available hard drive space  </li>
					<li>DVD Drive</li>
					<li>USB 2.0 port</li>
					<li>Mouse or other pointing device</li>
					<li>High resolution 19” TFT color monitor </li>
					<li>Laser printer, BW, 1200×1200 dpi, up to 25 pages/min </li>
				</ul>

				<hr className='class-2'/>
				<h4>2.2 Software Requirements </h4>
					<p>
						TargetQuan requires the following software:
					</p>
					<ul>
						<li>Microsoft™ Windows™ 7 32 bit, or Windows 7 64 bit</li>
						<li>Microsoft™.NET Framework 4.0</li>
					</ul>
					<p>
						<b>NOTICE : </b> Only the US English versions of the software that is listed in  this topic can be used
					</p>
					<p>
						You can install TargetQuan 3.2 as a standalone program. Xcalibur or  Foundation are not required. If required, however, you can use  TargetQuan 3.2 with the following software: 
					</p>
					<ul>
						<li>Thermo Foundation 2.0 SP1</li>
						<li>Xcalibur 2.2 SP1 </li>
						<li>or</li>
						<li>Thermo Foundation 3.0 SP2  </li>
						<li>Xcalibur 3.0</li>
					</ul>
					<p>
					<b>NOTICE : </b> TargetQuan 3.2 is not compatible with other versions of  TargetQuan, Thermo Foundation or Xcalibur. If other versions of  TargetQuan, Thermo Foundation or Xcalibur are present, they must be  uninstalled before attempting to install TargetQuan 3.2.
					</p>
				<h6>Additional Software</h6>
					<p>
						In addition to the above software, the following programs should be  installed on your computer: 
					</p>
					<ul>
						<li>Adobe™ Reader™ to display report documents </li>
						<li>You can install Adobe Reader from the TargetQuan 3.2 Software  DVD</li>
						<li>Text editor to display and edit script and log files  A basic text editor (Notepad) is part of Microsoft™ Windows™</li>
						<li>Microsoft Office™ 2007 or newer to display *.xls files</li>
						
					</ul>


				<hr className='class-1'/>
			<h2>3. Installing the Software</h2>
				<p>
					<b>NOTICE : </b> Before installing TargetQuan 3.2 on your computer, uninstall  previous versions of TargetQuan by using the Control Panel in  Microsoft™ Windows™.
				</p>
			<h6>❖ To install TargetQuan</h6>
				<ol>
					<li><img src={`${process.env.PUBLIC_URL}/images/targetquan/img_06.png`} width="auto"  alt=''></img><br/>
					<b>Figure</b> 1-1. Setup dialog box 
					</li>
					<li>Click TargetQuan 3.2 (for Xcalibur &lt;Xcalibur version&gt;) to start  the installation of TargetQuan.  <br/>
						It is important to install the version of TargetQuan paired with the  correct Xcalibur version. 
						To install a standalone version of  TargetQuan, click either button.  </li>
					<li>The Welcome dialog box of the InstallShield™ Wizard is displayed,  
						which guides you through the rest of the installation process. (See  <b>Figure</b> 1-2.) Click Next.<br/>
						<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_08.png`} width="auto"  alt=''></img><br/>
						<b>Figure</b> 1-2. InstallShield Wizard Welcome dialog box  <br/>
						<b>NOTICE : </b> The InstallShield Wizard will skip next two steps 
						if the  Microsoft™ Visual C++™ 2010 runtime libraries are already installed on  your computer.
					</li>
					<li>
						On the next dialog box, read the software license terms for installing  
						the Microsoft Visual C++ 2010 runtime libraries. See <b>Figure</b> 1-3.<br/>
						a. Select the topmost check box to confirm that you have read and  
						accepted the license terms. The Install button becomes  available.<br/>
						b. Click Install to continue the installation.  <br/>
						<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_10.png`} width="auto"  alt=''></img><br/>
						<b>Figure</b> 1-3. Software license terms for Microsoft Visual C++ 2010 runtime  libraries 
					</li>
					<li>On the next dialog box, click Finish to confirm that the installation  
						of the runtime libraries is complete and to continue. See <b>Figure</b> 1-4.
						<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_11.png`} width="auto"  alt=''></img><br/>
						<b>Figure</b> 1-4. Microsoft Visual C++ installation complete  
					</li>
					<li>
						The software now checks whether your computer meets the  minimum requirements for installing TargetQuan. 
						The result is  displayed on a dialog box. See <b>Figure</b> 1-5. Click Next to continue.  <br/>
						<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_13.png`} width="auto"  alt=''></img><br/>
						<b>Figure</b> 1-5. Minimum Product Requirements  
					</li>
					<li>
						A dialog box appears that shows the license agreement for  TargetQuan. See <b>Figure</b> 1-6.<br/>
						a. Read the license agreement. <br/>
						b. Click the corresponding option button to accept the agreement.  The Next button becomes available.<br/>
						c. Click Next to continue the installation of TargetQuan. <br/>
						<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_15.png`} width="auto"  alt=''></img><br/>
						<b>Figure</b> 1-6. License Agreement Dialog Box 
					</li>
					<li>
					The Thermo License Activation dialog box appears. 
					Enter the  activation code (license key) for TargetQuan and fill out the  mandatory fields in the Contact Information area. 
					The system  automatically retrieves the hardware ID of your computer. See  <b>Figure</b> 1-7. <br/>
					<b>NOTICE : </b> Each DVD comes with a license key. The license is valid for  either 2 installations or 20 installations.
					</li>
					<li>
					Click Activate to activate your TargetQuan license over the Internet.  <br/>
					If your computer is not connected to the Internet, follow the  instructions on page 1-13.  <br/>
					<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_17.png`} width="auto"  alt=''></img><br/>
					<b>Figure</b> 1-7. License Activation dialog box
					</li>
					<li>
						A dialog appears that shows the destination folder of the program  files. 
						See <b>Figure</b> 1-8. Click Next to continue the installation of  
						TargetQuan. Click Browse to open a dialog box where you can  select another folder.<br/>
						<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_19.png`} width="auto"  alt=''></img><br/>
						<b>Figure</b> 1-8. Choosing the installation folder  
					</li>
					<li>
						A dialog box informs you that the installation wizard is now ready to  
						begin the installation. See <b>Figure</b> 1-9. Click Install to continue. <br/>
						<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_21.png`} width="auto"  alt=''></img><br/>
						<b>Figure</b> 1-9. Wizard is ready for installation 
					</li>
					<li>
						After the installation, a dialog box displays the path of the Software  Installation Qualification Report. 
						If you want to see the report, click  Yes, otherwise click No.  <br/>
						<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_23.png`} width="auto"  alt=''></img><br/>
						<b>Figure</b> 1-10. Installation Qualification dialog box
					</li>
					<li>
						When the Setup Complete dialog box is displayed, click Finish to  exit the installation program. (See <b>Figure</b> 1-11.)<br/>
						<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_24.png`} width="auto"  alt=''></img><br/>
						<b>Figure</b> 1-11. TargetQuan installation completed  
					</li>
				</ol>

				 <hr className='class-2'/>
				<h4>3.1 Activating the TargetQuan License on a Computer	Without Internet Connection </h4>
					<p>
						This topic describes how to activate your TargetQuan license if you  want to use the program on a computer 
						that has no access to the  Internet. The instructions assume that the following requirements are  met:  
					</p>
					<ul>
						<li>On the computer without access to the Internet, you have started  the installation of TargetQuan as described above.</li>
						<li>In the Thermo License Activation dialog box, you have entered the  
							activation code for TargetQuan and filled out the mandatory fields  in the Contact Information area. </li>
						<li>The system has retrieved the hardware ID of your computer.</li>
						<li>You have (temporary) access to another computer with a working  
							connection to the Internet. TargetQuan 3.2 is not installed on this  computer! </li>
						<li>You have prepared an USB stick with sufficient free memory. </li>
					</ul>
					<h6>❖ To export a license request file </h6>
					<ol>
						<li>
							On the computer without access to the Internet, click the Export  
							hyperlink in the area at the bottom of the Thermo License  Activation dialog box. 
							See <b>Figure</b> 1-12. Do not click the Activate  button!  <br/>
							<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_26.png`} width="auto"  alt=''></img><br/>
							<b>Figure</b> 1-12. License activation hyperlinks


						</li>
						<li>
							In the file selection dialog box named “Export license activation  request”, 
							browse to the USB stick and save the licrequest file under a  name of your choice.
						</li>
						<li>
							Remove the USB stick.
						</li>
						<li>
							Remove the TargetQuan 3.2 Software DVD from the computer.
						</li>
					</ol>
					<h6>❖ To import a license request file </h6>
					<ol>
						<li>
							On the computer with Internet connection, 
							insert the  TargetQuan 3.2 Software DVD. The DVD automatically loads the  Xcalibur Setup program. <br/>
							<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_28.png`} width="auto"  alt=''></img><br/>
							<b>Figure</b> 1-13. Setup dialog box 
						</li>
						<li>
							Click Offline Activation to display the Thermo License Activation  dialog box. <br/>
							<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_29.png`} width="auto"  alt=''></img><br/>
							<b>Figure</b> 1-14. License Activation dialog box 
						</li>
						<li>
							Insert the USB stick with the licrequest file. 
						</li>
						<li>
							Click the Import hyperlink in the area at the bottom of the dialog  box. See <b>Figure</b> 1-12.
						</li>
						<li>
							In the file selection dialog box named “Import license activation  request”, 
							browse to the USB stick and select the licrequest file that  was created on the computer without Internet access. 
						</li>
						<li>
							Click Open to import the file and close the file selection dialog box  dialog box.
						</li>
						<li>
							On the Thermo License Activation dialog box, click Activate to  activate your TargetQuan license over the Internet. 
						</li>
						<li>
							After the successful activation, 
							a message box informs you that the  licrequest file on the USB stick was changed. Click OK to close the  dialog box. <br/>
							<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_31.png`} width="auto"  alt=''></img><br/>
							<b>Figure</b> 1-15. License request file was updated (example)  
						</li>
						<li>
							Remove the USB stick from the computer.  
						</li>
					</ol>
					<p>Now, you can abort the installation of TargetQuan on the computer  with Internet access. This computer is no longer necessary. </p>
					
					<h6>❖ To reimport a license request file</h6>
					<ol>
						<li>On the computer without Internet access, insert the USB stick with  the updated licrequest file. </li>
						<li>Click the Reimport hyperlink in the area at the bottom of the  Thermo License Activation dialog box. See <b>Figure</b> 1-12.</li>
						<li>In the file selection dialog box named “Import license activation  request”, browse to the USB stick and select the licrequest file that  was created on the computer with Internet access. </li>
						<li>Click Open to import the file and close the file selection dialog box. </li>
						<li>On the Thermo License Activation dialog box, click Activate to  activate your TargetQuan license.  </li>
						<li>
						A message box informs you that your license was successfully  activated. Click OK to close the message box. <br/>
							<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_33.png`} width="auto"  alt=''></img><br/>
							<b>Figure</b> 1-16. Activation of imported license successful 
						</li>
					</ol>
					<p>Now, continue the installation of TargetQuan as described on page 1-9. </p>


				<hr className='class-2'/>
				<h4>3.2 Starting TargetQuan</h4>
				<h6>❖ To start TargetQuan  </h6>
					Choose <b>Start &gt; All Programs &gt; Thermo TargetQuan &gt;  TargetQuan Quantification.  </b>

				<hr className='class-1'/>
			<h2>4. Installation Directory Usage</h2>
				<p>On both Microsoft™ Windows™ 7 and Windows™ XP systems,  
					TargetQuan is installed to C:\Program Files\Thermo\Thermo  TargetQuan 3.2\. </p>
				<p>If Xcalibur is not installed on your computer, example files and  configuration files are copied to C:\ProgramData\Thermo\Thermo  TargetQuan 3.2. If Xcalibur is installed on your computer, 
					C:\Xcalibur is  used as directory for the data files with both operating systems. </p>
				<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_05.jpg`} width="auto"  alt=''></img><br/>


    </div>


  )
}
