import React from 'react';
// 

function TargetQuan_3() {
	return (
		<>
			<div className="px-4" style={{marginTop:30}}>
					<MainContents />
			</div>
		</>
	);
};
export default TargetQuan_3
function MainContents() {
	const title = 'Chapter 3 Getting Started with TargetQuan';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			<h2>1. Getting Started with TargetQuan</h2>
				<p>
				This chapter explains the basic principles and procedures of a  quantitation process. 
				To minimize the time for following this example,  this chapter uses some files that already contain the necessary data.
				</p>
				<p>
				<b>NOTICE : </b> A thorough understanding of the principles and procedures  
				described in this chapter is a prerequisite for performing the automatic  data processing with TargetQuan.
				</p>
				<p>
				For a detailed description of the procedures in TargetQuan, see  Chapter 8: “Procedures in TargetQuan”.
				</p>
			
			<hr className='class-1'/>
			<h2>2. File Types Used with TargetQuan  </h2>

			<p>
			The following file types are used in TargetQuan: 
			</p>
				<table  className='table  table-bordered table-striped text-center'>
					<tbody>
						<tr className='table-info'>
							<th>Extension</th>
							<th>File type   </th>
							<th>Description </th>
						</tr>
						<tr>
							<td>*.sld </td>
							<td>Xcalibur<br/>
							Sequence file</td>
							<td>
								The sequence file was created with Xcalibur. The raw files (see below) should be
								found under the path specified in the Xcalibur sequence. <br/>
								<b>NOTICE : </b> No Xcalibur sequence file is required if a new TargetQuan sequence file  is created.
							</td>
						</tr>
						<tr>
							<td>*.resp </td>
							<td>Response File</td>
							<td>
								The response file contains relative response factors (RRF) calculated from
								standard samples. Either an already existing file can be attached and be used to  
								calculate amounts and so on in sample extracts. 
								Or, a new response file can be  created calculating RRFs from standard files 
								attached to the sequence.
							</td>
						</tr>
						<tr>
							<td>*.quan</td>
							<td>Quan file</td>
							<td>
								The quan file specifies the data evaluation method. It lists and names the analytes,
								retention times, masses, dependencies of native compounds and different types of  
								internal standards, TEF values, Totals, and so on. It can contain up to  
								165 parameters. One single file is used for a specific application only; different  
								standard concentrations are set on the Setup pane of the Sequence page. 
							</td>
						</tr>
						<tr>
							<td>*.qseq</td>
							<td>Sequence file</td>
							<td>
								Better called “TargetQuan sequence file”, this sequence file stores the and contains 
								all result data and related files after data evaluation. It is actually a zip archive file;  
								for reading, TargetQuan unpacks into a temporary folder in the background. The  
								complete results from a batch data evaluation can be transferred or archived via  this file. <br/><br/>
								<b>NOTICE : </b> The files contained in the sequence can be exported (extracted) to be  
								reused within another TargetQuan data evaluation setup. 
								For example, a created  response file can be exported to be used within another sequence.
							</td>
						</tr>
						<tr>
							<td>*.raw </td>
							<td>Raw file </td>
							<td>
								The raw files were created with Xcalibur and contain measurement data. If you are 
								importing an Xcalibur sequence, they must be found in the path defined under  the Xcalibur sequence file. 
							</td>
						</tr>
						<tr>
							<td>*.pdf </td>
							<td>Report file</td>
							<td>
								Report documents are optional and used for electronic distribution of the 
								quantitation results. They are created as pdf files and displayed in the Adobe™  Reader™. The information displayed in a report document is specified by the  settings in the Report Document group of the Application Properties pane of the  Quantitation page. 
							</td>
						</tr>
					</tbody>
				</table>
			
			<h6>Auxiliary Files</h6>
				<p>In the background, TargetQuan uses or creates other file types that serve  various purposes: </p>
				<table  className='table  table-bordered table-striped text-center'>
					<tbody>
						<tr className='table-info'>
							<th>Extension</th>
							<th>File type   </th>
							<th>Description </th>
						</tr>
						<tr>
							<td>*.qlay<br/>
								*.slay   </td>
							<td>Screen layout file</td>
							<td>
								Screen layouts store the arrangement of the panes on the tab pages. TargetQuan
								distinguishes between screen layouts for the Sequence page (*.slay) and for the 
								Quantitation page (*.qlay). 
							</td>
						</tr>
						<tr>
							<td>*.ucfg   </td>
							<td>Spreadsheet layout File</td>
							<td>
								Spreadsheet layouts store the arrangement of the data in a quan file in a defined  manner. 
								In addition to managing compounds and parameters, you can use  spreadsheet layouts to store sorting orders and data filter settings. 
								TargetQuan  displays the available spreadsheet layouts in the Spreadsheet Layout Wizard. 
							</td>
						</tr>
						<tr>
							<td>*.dref  </td>
							<td>Mass reference file</td>
							<td>
							Mass reference files contain accurate mass and intensity information of target 
							compounds and ISTD. They can be used for setting up quan masses and ratio  masses.  
							</td>
						</tr>
						<tr>
							<td>*.txta  </td>
							<td>Log file</td>
							<td>
								Log files are ASCII text files that record the activities of TargetQuan and error  messages as well. Additionally, 
								TargetQuan creates log files that record its activities  on the sequence level.
							</td>
						</tr>
						
					</tbody>
				</table>

				<hr className='class-2'/>
				<h4>2.1 Registering TargetQuan Files in Windows</h4>
					<p>
					You can open qseq files and resp files in TargetQuan by double-clicking  
					them in a file manager. To do so, you have to register them in  Microsoft™ Windows™. 
					The following example describes how to register  qseq files in Windows. 
					</p>
				<h6>❖ To register qseq files in Windows  </h6>
				<ol>
					<li>
					In a file manager, browse to the location of the qseq files. Standard  icons to the left of the qseq files indicate that these files are not  
					registered in Windows. Double-click a qseq file.<br/>
					<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_43.png`} width="auto"  alt=''></img><br/>
					<b>Figure</b> 3-1. Files are unknown to Windows
					</li>
					<li>
					Windows displays a dialog box that informs you that the file type is  not assigned to a program.<br/>
					<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_45.png`} width="auto"  alt=''></img><br/>
					<b>Figure</b> 3-2. Message: Windows can’t open file 
					</li>
					<li>
					Select the second option (Select a program from …) to display a list  of installed programs. <br/>
					<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_46.png`} width="auto"  alt=''></img><br/>
					<b>Figure</b> 3-3. Display list of installed programs 
					</li>
					<li>
					The next dialog box shows a list with icons of available programs. <br/>
					<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_48.png`} width="auto"  alt=''></img><br/>
					<b>Figure</b> 3-4. List of available programs 
					</li>
					<li>
					In the dialog box, browse to the installation directory for  TargetQuan (for example, C:\Program Files\Thermo\Thermo  TargetQuan 3.2\System\TQProgram). <br/>
					<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_50.png`} width="auto"  alt=''></img><br/>
					<b>Figure</b> 3-5. Selecting the TargetQuan executable file  
					</li>
					<li>Select the TargetQuan executable file and click <b>Open</b>. </li>
					<li>The dialog box shown in <b>Figure</b> 3-4 is displayed again and contains  now an icon for TargetQuan. </li>
					<li>Select the icon and click <b>OK</b>. The registration is finished and the  dialog box is closed.</li>
				</ol>
				<p>
				You can now open qseq files in TargetQuan by double-clicking them. A  new icon to the left of the file name indicates that it is registered in  Windows. See <b>Figure</b> 3-6.  
				</p>

				<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_51.png`} width="auto"  alt=''></img><br/>
				<b>Figure</b> 3-6. q seq files are registered in Windows  

				<p>To register resp files, double-click a file and follow the instructions  above. At present however, you cannot open quan files by  double-clicking them. </p>


			<hr className='class-1'/>
			<h2>3. Performing a Quantitation with TargetQuan</h2>
			<p>
			The following example describes a quantitation with TargetQuan  according to the EPA1613 method.
			</p>
			<p>
			<b>NOTICE : </b> We recommend becoming familiar with one of the  international methods (for example: EPA1613, JIS), abbreviations,  dependencies, and settings that are used in our files.
			</p>
			<p>
			In the EPA1613 method, a calibration curve is created with five  calibration points. Therefore, you have to perform a quantitation with  five calibration solutions. The directory TQ_30_default that is used in  the following example holds the results for five different concentrations  similar to EPA1613’s CS1 to CS5 calibration solutions. The quan file is  ready prepared to work with the corresponding raw files. All parameters  are optimized for the different concentrations or they are set in  correlation to method EPA 1613 (ratio limits 15ꢀ, for example).  
			</p>
			<p>
			For the calculation, TargetQuan requires information about the masses  that are used and the retention times when they are to be expected.  Additionally, you need data of integration parameters, amounts of  standards and native components. This information is stored in the  master quan file (CS1_DIOXIN_BATCHMASTER.quan, for  example). The calculated results are stored in the sequence file  (TQ30_example_sequence.qseq, for example). 
			</p>
			<p>
			During the quantitation of each calibration solution, TargetQuan uses  the data of the raw file and the quan file to detect and integrate the  peaks of the congeners. Then it calculates the response factor, the  relationship between signal intensity of a peak and the known amount.  Each peak intensity/amount pair represents a calibration point. With  five calibration points it creates a calibration curve. All calibration curves  of the congeners are stored in the response file. See <b>Figure</b> 3-7.
			</p>

			<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_08.jpg`} width="auto"  alt=''></img><br/>
			<b>Figure</b> 3-7. Step 1: Creating a response file

			<p>Then TargetQuan uses the response file to determine the unknown  amounts of the natives in each sample. See <b>Figure</b> 3-8. </p>

			<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_09.jpg`} width="auto"  alt=''></img><br/>
			<b>Figure</b> 3-8. Step 2: Quantitating the Unknowns




			<hr className='class-1'/>
			<h2>4. Preparing the Training Files</h2>
			<p>
			In this practical introduction, you will use a set of files that is available  on the TargetQuan 3 Software DVD. Those files are stored in the  directory TQ_30_default. 
			</p>
			<h6>❖ To copy the required files to the training directory</h6>
			<ol>
				<li>If Xcalibur is not installed on your computer, create a directory  C:\Xcalibur\data.</li>
				<li>Copy the directory TQ_30_default from the TargetQuan 3  Software DVD to C:\Xcalibur\data. 
					<ul>This directory (see <b>Figure</b> 3-9) contains the following files: 
						<li>the raw files for the calibration solutions  (cs1_test.raw–cs5_test.raw), </li>
						<li>the raw file with the unknowns (cs_test_unknown.raw),  </li>
						<li>the quan file for the calibration solution with the lowest  concentration (CS1_DIOXIN_BATCHMASTER.quan), </li>
						<li>the Xcalibur sequence (TQ_30_Xcal_Sequence.sld) that will be  used to create the TargetQuan sequence file,</li>
						<li>a Microsoft™ Word document with a short installation  instruction (README_TQ_demo_data.doc).  </li>
						
					</ul>
				</li>
			</ol>

			<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_55.png`} width="auto"  alt=''></img><br/>
			<b>Figure</b> 3-9. TQ_30_default directory

			<p><b>NOTICE : </b> The path C:\Xcalibur\data\TQ_30_default\ is specified in the  default sequence file (TQ_30_Xcal_Sequence.sld). If it is changed,  TargetQuan will not be able to find the data files for the data evaluation  process.</p>



			<hr className='class-1'/>
			<h2>5. Performing a Quantitation</h2>
			<p>This topic describes how to create the sequence and how to perform the  batch process data evaluation. </p>
			
			<h6>❖ To create the TargetQuan sequence </h6>
			<ol>
				<li>Choose <b>Start</b> &gt; <b>All Programs</b> &gt; <b>Thermo TargetQuan</b> &gt;  <b>TargetQuan Quantification</b> to start TargetQuan. </li>
				<li>From the Sequence page, choose <b>File</b> &gt; <b>New</b>.  </li>
				<li>In the dialog box, click Yes to import an existing Xcalibur quan  sequence. See <b>Figure</b> 3-10.  <br/>
					<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_57.png`} width="auto"  alt=''></img><br/>
					<b>Figure</b> 3-10. Creating a new Sequence
				</li>
				<li>
				In the Open dialog box, browse for the training directory. Select the  sequence file TQ_30_Xcal_Sequence.sld. Click Open to close the  dialog box.<br/>
					<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_59.png`} width="auto"  alt=''></img><br/>
					<b>Figure</b> 3-11. Importing an Xcalibur sequence  <br/>
					The content of the Xcalibur sequence is shown in <b>Figure</b> 3-12.  <br/>
					<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_60.png`} width="auto"  alt=''></img><br/>
					<b>Figure</b> 3-12. Xcalibur training sequence   <br/>
				</li>
				<li>
					A dialog box informs you that you have to enter the path and the  name of the new quan sequence. Click <b>OK</b> to confirm your action. <br/>
					<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_62.png`} width="auto"  alt=''></img><br/>
					<b>Figure</b> 3-13. Path and name of new sequence required 
				</li>
				<li>
				In the Open dialog box, browse for the directory C:\Xcalibur\data.  
				Enter the name of the sequence file (TQ30_example_sequence.qseq,  for example). 
				Click <b>Open</b> to create the sequence file and to close the  dialog box. <br/>
					<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_63.png`} width="auto"  alt=''></img><br/>
					<b>Figure</b> 3-14. Entering path and name of new sequence  
				</li>
				<li>
				The sequence file is saved in the target directory. <br/>
					<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_64.png`} width="auto"  alt=''></img><br/>
					<b>Figure</b> 3-15. Saving the new sequence  
				</li>
				<li>
				The Sequence page now shows the content of the imported Xcalibur  sequence.<br/>
					<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_66.png`} width="auto"  alt=''></img><br/>
					<b>Figure</b> 3-16. Imported Xcalibur sequence 
				</li>
			</ol>

			<h6>❖ To complete the sequence setup  </h6>
				<ol>
					<li>To load the quan file into the sequence, click into the Method Quan  File line of the Setup pane to display the 
						<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_67.png`} width="auto"  alt=''></img>
						button. </li>
					<li> Click the  
						<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_67.png`} width="auto"  alt=''></img>
						button to display the Open dialog box. </li>
					<li> In the Open dialog box, browse for the training directory. Select the  file CS1_DIOXIN_BATCHMASTER.quan. Click <b>Open</b> to close  the dialog box.<br/>
					<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_69.png`} width="auto"  alt=''></img><br/>
					<b>Figure</b> 3-17. Selecting the training quan file
					</li>
					<li>To create a new response file from the standards, click into the  Response file field and enter the name of the response file  (NEW_RESP, for example). TargetQuan will use this response file  to quantitate the unknown amounts in the last file. </li>
					
					
				</ol>
				<p>The Setup pane now shows all required files of the TargetQuan  sequence. </p>
				<br/>
					<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_70.png`} width="auto"  alt=''></img><br/>
					<b>Figure</b> 3-18. Setup of the training sequence  

				<h6>❖ To complete the sample settings</h6>
				<ol>
					<li>
						In the sequence table, change the sample type of the first five rows to  <b>Std. Clear</b>.<br/>
						<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_72.png`} width="auto"  alt=''></img><br/>
						<b>Figure</b> 3-19. Setting the sample type for the standards<br/>
						<b>NOTICE : </b> TargetQuan treats all samples of the type Std… as standards.
					</li>
					<li>
						Set the response mode of the first five rows to <b>Single Point</b>  (<b>Spec. RF</b>). <br/>
						<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_73.png`} width="auto"  alt=''></img><br/>
						<b>Figure</b> 3-20. Setting the response mode for the standards
					</li>
					<li>
					The quan method file contains the lowest concentration level of the  standards, which is here the EPA 1613 CS1 level. To specify the  relative concentrations for the other standard solutions, enter the  respective multiplication factors in column UNK of the sequence  table:<br/>
					CS2 = <b>4</b>×CS1, CS3 = <b>20</b>×CS1, CS4 = <b>80</b>×CS 1, CS5 = <b>400</b>×CS1 
					</li>
					<li>
					Choose <b>File</b> &gt; <b>Save</b> to save your changes. <b>Figure</b> 3-21 shows the  final sequence setup. 
					(For clarity, empty columns of the sequence  table are hidden.)<br/>
						<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_75.png`} width="auto"  alt=''></img><br/>
						<b>Figure</b> 3-21. Final sequence setup
					</li>
				</ol>

				<h6>❖ To perform the batch process data evaluation</h6>
					<p>
					To start the process, click the  
					<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_76.png`} width="auto"  alt=''></img>
					button. You will now <b>NOTICE : </b>  certain activities on the Sequence page. 
					Each row of the sequence as  defined above is evaluated (peak integration and calculations). 
					For  each successfully processed row, TargetQuan displays an  
					<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_77.png`} width="auto"  alt=''></img>
					symbol  and changes the process status to “processed”. 
					You can also follow  the progress on the Output pane. 
					The process is finished when the  Output pane shows “Finish Process Sequence” as last line. See  <b>Figure</b> 3-22. 
					</p><br/>
					<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_78.png`} width="auto"  alt=''></img><br/>
					<b>Figure</b> 3-22. Finished sequence 

				<h6>❖ To display the quantitation results </h6>
				<ol>
					<li>
					Click the <b>Quantitation</b> tab to display the integration and  calculation results. 
					Scroll through the rows of the sequence view by  clicking the  
					<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_77.png`} width="auto"  alt=''></img>
					symbol to select a file (CS1, for example). 
					Click  through the rows of the spreadsheet layout to select a specific target  analyte (native 2378-TCDD, for example). 
					Note that the Response  File column shows the name of the created response file. 
					Display the  integration result in the Chromatogram view. See <b>Figure</b> 3-23. <br/>
						<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_80.png`} width="auto"  alt=''></img><br/>
						<b>Figure</b> 3-23. Native TCDD in CS 1 
					</li>
					<li>
					Click the <b>Response</b> tab to display the Response page.  
					</li>
					<li>
					Click the  
					<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_81.png`} width="auto"  alt=''></img>
					button to update the view of the Response page. 
					The  Response page shows the response file created during the sequence  run. See <b>Figure</b> 3-24.  <br/>
						<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_83.png`} width="auto"  alt=''></img><br/>
						<b>Figure</b> 3-24. Response file after sequence run 
					</li>
				</ol>


				<hr className='class-2'/>
				<h4>5.1 Distributing Quantification Results </h4>
					<p>
					You can now use one of the available spreadsheet layouts to present the  results of the quantification. Alternatively, create an individual layout.  See “Spreadsheet Layouts” on page 10-1 for further information. For  instructions about creating an individual quantification report, see  “Creating a Quantification Report” on page 8-7. 
					</p>
					<p>
					To store the results in a different form, export the results in other file  format (*.txt or *.xls, for example). See “Exporting Data from Quan  Files” on page 8-13 for details. 
					</p>
					<p>
					Finally, you can print out the quantification report. Alternatively, you  can create a report document. In addition to the quantification results, it  allows enclosing all relevant settings for the quantification. See  “Creating Report Documents for Quan Files” on page 8-11 for details. 
					</p>


    </div>


  )
}
