import React from 'react';
// 

function TargetQuan_5() {
	return (
		<>
			<div className="px-4" style={{marginTop:30}}>
					<MainContents />
			</div>
		</>
	);
};
export default TargetQuan_5
function MainContents() {
	const title = 'Chapter 5 Sequence Page';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<p>The Sequence page shows the properties of the active sequence file. This  page is shown when TargetQuan is started.  </p>

			<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_198.png`} width="auto"  alt=''></img><br/>
				<b>Figure</b> 5-1. Sequence page

				<p>The Sequence page comprises the following panes:  <br/>
				• Setup Pane  <br/>
				• Sequence Table  <br/>
				• Output Pane  <br/>
				• Overview Pane </p>

			<p>The Sequence page provides a shortcut menu, which you can display by  right-clicking elements displayed on the page. The available commands  depend on the active view and are described in “Shortcut Menus” on  page 4-20. </p>
			<p>In the default arrangement, the Sequence page shares the space with the  Quantitation page and the Response page. To display the Sequence page  permanently, drag its tab to a new location to separate it from the other  tabs. With exception of the Sequence Table, you can rearrange the panes  on the Sequence page. See “Rearranging Pages and Panes” on page 8-2  for details. </p>

			<h6>❖ To display the Sequence page</h6>
			<p>Click the <b>Sequence</b> tab of the TargetQuan window.  </p>





			<hr className='class-1'/>
			<h2>1. Setup Pane </h2>

			<p>Use the Setup pane to specify global properties of the sequence. The files  currently in use are shown in the respective text boxes. The Setup pane  has the following parameters:  </p>

				<table  className='table  table-bordered table-striped'>
					<tbody>
						<tr className='table-info'>
							<th>Parameter</th>
							<th>Description </th>
						</tr>
						<tr>
							<td>Sequence File  </td>
							<td>Displays path and name of the active  TargetQuan sequence file (*.qseq). To  change the file, use the  button on the  right side of the text box. </td>
						</tr>
						<tr>
							<td>Method Quan File</td>
							<td>Displays path and name of the active  quan file. To change the file, use the  button on the right side of the text box.</td>
						</tr>
						<tr>
							<td>Response File</td>
							<td>Displays the name of the active response  file. To change the file, use the  on the right side of the text box.</td>
						</tr>
						<tr>
							<td>Xcalibur Sequence File  </td>
							<td>Displays path and name of the imported  Xcalibur sequence. If the active sequence  was created new, this text box is empty.  </td>
						</tr>
						<tr>
							<td>Raw Source Path </td>
							<td>Displays the path of the first raw file  referenced by the imported Xcalibur  sequence. If the active sequence was  created new, this text box is empty. </td>
						</tr>
					</tbody>
				</table>

			
			<hr className='class-1'/>
			<h2>2. Sequence Table </h2>

			<p>Each row of the sequence table corresponds to one sample injection.  Each sample is defined by its sequence row settings. Use the Columns  dialog box to select the columns that are displayed.  <br/>
				In the text boxes of the sequence table, you can use the commands of the  shortcut menu to copy content from one table cell to another.  <br/>
				To restrict operations (for example, creating reports) to some samples,  you can select multiple table rows:  
				• To select consecutive rows, click into the first row. Then press the  &lt; Shift &gt; key and click into the last row.  <br/>
				• To select rows that are not following one after another, click into the  first row. Then press the &lt;Ctrl&gt; key and click into the other rows.  
				The sequence table has the following parameters:  </p>


				<table  className='table  table-bordered table-striped'>
					<tbody>
						<tr className='table-info'>
							<th>Parameter</th>
							<th>Description </th>
						</tr>
						<tr>
							<td><img src={`${process.env.PUBLIC_URL}/images/targetquan/img_77.png`} width="auto"  alt=''></img></td>
							<td>If this icon is shown, the sample has been processed. To display the respective quan file  on the Quantitation page, click the icon.</td>
						</tr>
						<tr>
							<td><img src={`${process.env.PUBLIC_URL}/images/targetquan/img_115.png`} width="auto"  alt=''></img></td>
							<td>If this icon is shown, a report document (*.pdf) has been created for the sample. To  display the report document in the Adobe™ Reader™, click the icon.  </td>
						</tr>
						<tr>
							<td><img src={`${process.env.PUBLIC_URL}/images/targetquan/img_201.png`} width="auto"  alt=''></img></td>
							<td>If this icon is shown, an export document (*.xls) has been created for the sample. To  display the report document in Microsoft™ Excel™, click the icon. </td>
						</tr>
						<tr>
							<td>RawFile </td>
							<td>Displays the name of the raw file. To find and select the raw file (*.raw), double-click the  text box. In the Open dialog box, browse for the suitable directory and select the name of  the raw file. You can also type the path in the text box. </td>
						</tr>
						<tr>
							<td>Process Status </td>
							<td>Displays the process status of the sample. The following statuses are possible:  <br/>
							• not processed  <br/>
							• processed  <br/>
							• re-processed  <br/>
							• RF not applied  <br/>
							• passed  <br/>
							• finalized </td>
						</tr>
						<tr>
							<td>Sample Name  </td>
							<td>View the name that you assign to the sample</td>
						</tr>
						<tr>
							<td>Sample Type  </td>
							<td>Use the list box to select the type of sample described by the sequence row. Different sets  of sample types are presented for different bracket types. The sample type defines how  the data system processes the sample data. Each sample must be classified as one of the  following Xcalibur sample types:<br/>
							  • Unknown: Sample with unknown quantities.  <br/>
								• Blank  <br/>
								• QC (quality control): Used for a standard when measuring unknown samples.  Calibration standards are run and quantitated as if they were a sample to evaluate  whether an existing response calibration curve is still valid.  <br/>
								• Std Clear  <br/>
								• Std Update  <br/>
								• Std Bracket: Used for a standard when creating a response file.  <br/>
								• Start Bracket  <br/>
								• End Bracket</td>
						</tr>
						<tr>
							<td>Resp Mode</td>
							<td>Use the list box to select the suitable response factor to correct for the different response  of the mass spectrometer to different compounds. The following options are available:  <br/>
							• Single Point (Spec RF)  <br/>Select this option when you are creating a response curve. With this option,  TargetQuan uses the specified response factors for the quantitation. No response file  is required. <br/>
							• Average RF  <br/>When you select this option, TargetQuan averages the RF values contained in the  attached response file. Average RF is used in most applications. <br/>
							• RF vs. <br/>Area/Height  When you select this option, TargetQuan computes a response curve by plotting the  Actual RF (A/H) values of a native against the measured peak areas/heights. Then  TargetQuan numerically interpolates this curve to calculate unknown amounts by  measured peak areas.<br/>
							• Ratio Areas/Height vs. Amount  <br/>When the measured peak areas of the calibration solution do not fit together (in case  of old solutions, for example), select this option to get more precise results. For each  native and its internal standard, TargetQuan calculates the ratio of the measured  peak areas/heights. Then it plots these ratios against the specified amounts.  TargetQuan calculates unknown amounts by using the response factor that is  returned by the resulting curve. <br/>
							<b>NOTICE : </b> You can use Average RF, RF vs. Area/Height and Ratio of Areas/Heights vs.  Amount only in combination with a response file.
							</td>
						</tr>
						<tr>
							<td>Response File</td>
							<td>Use this text box to enter the path to the response file. To find and select the path,  double-click the text box. In the Open dialog box, browse for the suitable directory and  select the name of the response file. You can also type the path in the text box. </td>
						</tr>
						<tr>
							<td>Method File </td>
							<td>Use this text box to enter the path to the quan file. To find and select the path,  double-click the text box. In the Open dialog box, browse for the suitable directory and  select the name of the quan file. You can also type the path in the text box. </td>
						</tr>
						<tr>
							<td>Smp Weight</td>
							<td>Use this text box to enter the amount of a component that has been placed in the sample.  The unit for this sample weight is specified in the Processing Setup window and is only  included in Xcalibur reports. The data system does not convert units.<br/>
							To change the sample weight, double-click the text box. The cursor changes to the  vertical bar cursor. Enter the correct sample weight. The cursor changes back to the  original cursor when you click any other area of the view.</td>
						</tr>
						<tr>
							<td>Smp Vol </td>
							<td>Use this text box to enter the volume of a component that has been placed in the sample.  The unit for this volume is specified in the Processing Setup window and is only  included in Xcalibur reports. The data system does not convert units.  <br/>
							To change the sample volume, double-click the text box. The cursor changes to the  vertical bar cursor. Enter the correct sample volume. The cursor changes back to the  original cursor when you click any other area of the view.  </td>
						</tr>
						<tr>
							<td>Dilution Factor  </td>
							<td>Use this text box to enter the dilution factor that was used to prepare the sample. The  valid range is 0.000 to 10000.000. TargetQuan interprets a value of 0.000 as no dilution. <br/>
							To change the dilution factor, double-click the text box. The cursor changes to the  vertical bar cursor. Enter the correct factor. The cursor changes back to the original  cursor when you click any other area of the view. </td>
						</tr>
						<tr>
							<td>SUR</td>
							<td>Use this text box to change the multiplication factor for the surrogate in the active  sample. TargetQuan uses this factor to calculate the amount of the surrogate based on  the amount that is contained in the quan file that is listed in the Setup pane. The default  factor is 1. <br/>
							To change the factor, double-click the text box. The cursor changes to the vertical bar  cursor. Enter the correct factor. The cursor changes back to the original cursor when you  click any other area of the view. </td>
						</tr>
						<tr>
							<td>IS  </td>
							<td>Use this text box to change the multiplication factor for the internal standard in the  active sample. TargetQuan uses this factor to calculate the amount of the internal  standard based on the amount that is contained in the quan file that is listed in the Setup  pane. The default factor is 1.<br/>
							To change the factor, double-click the text box. The cursor changes to the vertical bar  cursor. Enter the correct factor. The cursor changes back to the original cursor when you  click any other area of the view. </td>
						</tr>
						<tr>
							<td>UNK  </td>
							<td>Use this text box to change the multiplication factor for the unknowns in the active  sample. TargetQuan uses this factor to calculate the amount of the unknowns based on  the amount that is contained in the quan file that is listed in the Setup pane. The default  factor is 1. <br/>
							To change the factor, double-click the text box. The cursor changes to the vertical bar  cursor. Enter the correct factor. The cursor changes back to the original cursor when you  click any other area of the view.</td>
						</tr>
						<tr>
							<td>Report Layout </td>
							<td>Double-click the cell to open the Customize Layouts dialog box. Here, you can select  spreadsheet layouts that specify the data for the report document. The order of the  layouts in this cell defines the order of the data contained in the PDF report document.  If no report layout is selected, TargetQuan will not create a report document.  <br/>
							TargetQuan marks spreadsheet layouts that are designated for sequence operations by the  prefix “seq_”. Available spreadsheet layouts can be accessed via the Layouts section of the  Spreadsheet Layout Wizard on the Quantitation page. </td>
						</tr>
						<tr>
							<td>Export Layout  </td>
							<td>Double-click the cell to open the Customize Layouts dialog box. Use the dialog box to  select spreadsheet layouts that specify the data for the export documents. If no export  layout is selected, TargetQuan will not create an export document. You must also enter  an output path for the export documents. <br/>
							TargetQuan marks spreadsheet layouts that are designated for sequence operations by the  prefix “seq_”. Available spreadsheet layouts can be accessed via the Layouts section of the  Spreadsheet Layout Wizard on the Quantitation page. The format of the data that are  exported into an Microsoft™ Excel™ worksheet are specified by the current settings in the  ASCII Export Options dialog box. </td>
						</tr>
					</tbody>
				</table>
				

			<hr className='class-1'/>
			<h2>3. Output Pane </h2>
			<p>The Output pane shows the audit trail for the quantitation of the  samples contained in the active sequence. Use this pane to check  whether TargetQuan uses the correct settings (response factor, for  example) during the automatic procedure. </p>

			<p>The content of the Output pane is stored in the sequence log file, which  can be displayed by choosing <b>Sequence</b> &gt; <b>Show Sequence Logfile</b>. </p>
			
			<hr className='class-1'/>
			<h2>4. Overview Pane </h2>
			<p>Use the Overview pane to display the values that a certain quan  parameter has in all samples of the active sequence. See <b>Figure</b> 5-2. You  can choose the sample type and export the displayed data.  </p>

			<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_206.png`} width="auto"  alt=''></img>
			<b>Figure</b> 5-2. Overview pane

			<h6>Toolbar</h6>

			<table  className='table  table-bordered table-striped'>
					<tbody>
						<tr className='table-info'>
							<th>Button  </th>
							<th>Description </th>
						</tr>
						<tr>
							<td><img src={`${process.env.PUBLIC_URL}/images/targetquan/img_100.png`} width="auto"  alt=''></img></td>
							<td>Copy the content of the Overview pane to the  clipboard. </td>
						</tr>
						<tr>
							<td><img src={`${process.env.PUBLIC_URL}/images/targetquan/img_207.png`} width="auto"  alt=''></img></td>
							<td>Open a File save as dialog box to create *.txt and *.xls  files.  <br/>Before TargetQuan exports the data, you can enter  some export details in the ASCII Export Options  dialog box. See page 4-25. </td>
						</tr>
						<tr>
							<td><img src={`${process.env.PUBLIC_URL}/images/targetquan/img_208.png`} width="auto"  alt=''></img></td>
							<td>Display the values for the standards.</td>
						</tr>
						<tr>
							<td><img src={`${process.env.PUBLIC_URL}/images/targetquan/img_209.png`} width="auto"  alt=''></img></td>
							<td>Display the values for the QC type samples.</td>
						</tr>
						<tr>
							<td><img src={`${process.env.PUBLIC_URL}/images/targetquan/img_210.png`} width="auto"  alt=''></img></td>
							<td>Display the values for the unknowns.</td>
						</tr>
						<tr>
							<td><img src={`${process.env.PUBLIC_URL}/images/targetquan/img_211.png`} width="auto"  alt=''></img></td>
							<td>Remove the active quan parameter from the parameter  list box.  </td>
						</tr>
						<tr>
							<td><img src={`${process.env.PUBLIC_URL}/images/targetquan/img_212.png`} width="auto"  alt=''></img></td>
							<td>Use this list box to select the parameter.  <br/>See page 8-70 for instructions about specifying the  parameters displayed in the list box. </td>
						</tr>
			</tbody>
		</table>


    </div>


  )
}
