<p><b>High
Voltage.</b> High voltages that can cause an electric shock are used in the
instrument. Opening the instrument housing is only allowed for maintenance
purposes as described in this manual or by Thermo Fisher Scientific personnel.
To make sure that the instrument is free from all electric current, always
disconnect the power cord of the mass spectrometer before you try any type of
maintenance.</p>

