<p><b>High
Voltage. </b>Electric Shock Hazard. During operation, the direct probes, their
cabling, and the supply circuits are on 5 kV accelerating voltage. This voltage
is very dangerous! Make sure that the yellow/green ground cables of the probes
are connected to ground! For maintenance and repair, take the probes out of the
vacuum lock and disconnect the cabling! After maintenance and repair, make sure
that all parts at high voltage are covered and protected properly.</p>

