import React from 'react';

function DFS_OM_en_0() {
	return (
		<>
			<div className="px-4" style={{marginTop:30}}>
					<MainContents />
			</div>
		</>
	);
};
export default DFS_OM_en_0
function MainContents() {
	const title = 'Chapter 1. Using this Manual';


	return (
		<div>
			<h1 className='title'>{title}</h1>	
			
			<img src={`${process.env.PUBLIC_URL}/images/DFS_OM_EN/image001.png`} width="auto"  alt=''></img><br/>

			<h2>Magnetic Sector GC-HRMS Operating Manual</h2>

		</div>


  )
}