import React from 'react';

function TargetQuan_0() {
	return (
		<>
			<div className="px-4" style={{marginTop:30}}>
					<MainContents />
			</div>
		</>
	);
};
export default TargetQuan_0
function MainContents() {
	const title = 'TargetQuan Operating Manual';


	return (
		<div>
			<h1 className='title'>{title}</h1>	

			<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_04.jpg`} width="auto"  alt=''></img><br/>

		</div>


  )


}
