import React from 'react';

function DFS_Help_2() {
	return (
		<>
			<div className="px-4" style={{marginTop:30}}>
					<MainContents />
			</div>
		</>
	);
};
export default DFS_Help_2
function MainContents() {
	const title = 'Getting Started';

	return (
		<div>
			<h1 className='title' id="content0">{title}</h1>	
			<hr className='class-1'/>
      <h5>Contents</h5>
      <p>
      <a href='#content002'>2. Getting Started</a><br/>
      <a href='#content021'>2.1 Introduction</a><br/>
      <a href='#content022'>2.2 Getting Ions through Electron Ionization</a><br/>
      <a href='#content221'>  2.2.1 Changing the Spectrum Display</a><br/>
      <a href='#content222'>  2.2.2 Adjusting Peaks in the Spectrum View</a><br/>
      <a href='#content023'>2.3 Setting up DFS SmartTune</a><br/>
      <a href='#content024'>2.4 Autotuning the DFS Mass Spectrometer</a><br/>
      <a href='#content025'>2.5 Semi-Automatic Tuning</a><br/>
      <a href='#content026'>2.6 Manual Tuning</a><br/>
      <a href='#content027'>2.7 Troubleshooting: No Ions</a><br/>
      <a href='#content028'>2.8 Starting a New Scan</a><br/>
      </p>
			<hr className='class-1'/>

      <h2 id="content002">2. Getting Started</h2>
      <a href='#content0'>To Contents</a><br/>
        <p>
        <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Getting Started<br/>
        Before you start a measurement, you must tune the instrument for optimum performance. This topic describes how to prepare the instrument for measurement.<br/>
        Contents<br/>
        •  Introduction <br/>
        •  Getting Ions through Electron Ionization <br/>
        •  Setting up DFS SmartTune <br/>
        •  Autotuning the DFS Mass Spectrometer <br/>
        •  Semi-Automatic Tuning <br/>
        •  Manual Tuning <br/>
        •  Troubleshooting: No Ions <br/>
        </p>

			  <hr className='class-2'/>
        <h4 id="content021">2.1 Introduction</h4>
        <a href='#content0'>To Contents</a><br/>
          <p>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Introduction<br/>
          With the Tune window, you control the DFS Magnetic Sector GC-HRMS system from the data system computer.<br/>
          <b><u>DFS SmartTune window</u></b>  <br/>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_06.png`} width="auto"  alt=''></img><br/>
          You can start the instrument the way you learned from the Thermo Fisher Scientific field service engineer or from your colleague. Then start the DFS SmartTune software.<br/>
          
          <b><u>Standby Condition</u></b><br/>
          
          Because a longer period of pumping and warm-up is necessary to get stable vacuum conditions, do not turn off the mass spectrometer over night or for usual weekends. If possible, do not even turn it off for longer weekends or for longer vacation periods. Only when you expect interruptions of mains power and water supply, switch off the instrument completely.<br/>
          Usually, the data system stays connected to the mass spectrometer in Standby position. So, operation of the mass spectrometer can be started directly by opening the DFS SmartTune software. Refer to the DFS Operating Manual for additional information.<br/>
          <b>Tip</b>  Instruments mainly used for scanning should remain in scanning mode during standby. Instruments mainly used for MID work should be “parked” on a mass in the range from m/z = 200 to m/z = 500 (for example, m/z = 314). <br/>
          </p>

			  <hr className='class-2'/>
        <h4 id="content022">2.2 Getting Ions through Electron Ionization</h4>
        <a href='#content0'>To Contents</a><br/>
          <p>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Getting Ions through Electron Ionization<br/>
          <b>*  To start getting ions in the Electron Ionization (EI) mode</b> <br/>
          1.  Open the Tune window.  <br/>
          2.  Choose <b>Setup &gt; Change Ionization Mode</b> and check that the EI mode is selected. <br/>
          3.  Click the Spectrum View button or choose <b>View &gt; Display Spectrum View</b> to open the Spectrum View. <br/>
          4.  Click the <b>Standby</b> button to set the instrument to position ON. The startup procedures turns on the accelerating voltage, the filament current, the multiplier voltage, and the conversion dynode voltage. <br/>
          5.  Click the <b>Define Scan</b> button to display the Define Scan dialog box.  <br/>
          6.  If required, set the Scan Mode to Tune Sweep. <br/>
          7.  Enter the mass number of a suitable ion, for example m/z = 40 (argon), and set the sweep width to 2%. <br/>
          8.  Click <b>Apply</b> to start the scan with the current values set in the Define Scan dialog box. The acquired data should look as shown in this figure. <br/>
          
          <b><u>Ions in the Spectrum View</u></b>  <br/>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_09.png`} width="auto"  alt=''></img><br/>

          <b>Tip</b>  The intensity of the argon ion in your DFS mass spectrometer might be quite different from our example. <br/>
          If no ions are detected, proceed to Troubleshooting: No Ions.<br/>
          </p>

          <hr className='class-3'/>
          <h5 id="content221">2.2.1 Changing the Spectrum Display</h5>
          <a href='#content0'>To Contents</a><br/>
            <p>
            <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Changing the Spectrum Display<br/>
            Use the toolbar buttons above the spectrum to control the Spectrum View.<br/>

            <table className='table  table-bordered table-striped'>
            <tbody>
              <tr className='table-info'>
                <td>Button</td>
                <td>Description</td>
              </tr>
              <tr>
              <td><img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_10.png`} width="auto"  alt=''></img></td>
                <td>Sets the Y-axis to the height of the highest peak. 
                  Thus, the largest peak in the spectrum is always displayed at full scale.<br/>
                  <b>Tip</b>  When the peak intensities change, the scale will change as well.
                  </td>
              </tr>
              <tr>
              <td><img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_12.png`} width="auto"  alt=''></img></td>
                <td>Sets the spectrum view to Creep mode: the Y scale of the mass spectrum automatically increases 
                  when the peak intensity increases, but does not decrease when the peak intensity decreases.</td>
              </tr>
              <tr>
              <td><img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_14.png`} width="auto"  alt=''></img></td>
                <td>Sets the spectrum view to fixed mode: the height of the y axis is set equal to the height of the 
                  largest peak in the current spectrum and remains fixed even if larger peaks occur.</td>
              </tr>
              <tr>
              <td><img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_15.png`} width="auto"  alt=''></img></td>
                <td>Zooms in the spectrum view on the X-axis with the mouse. To zoom in on a portion of the X-axis, 
                  drag the mouse pointer (hand cursor) from the beginning to the end of the portion you want to see enlarged. 
                  To return to a display of the full mass spectrum, click the <b>Display All</b> button.</td>
              </tr>
            </tbody>
          </table>
            </p>

          <hr className='class-3'/>
          <h5 id="content222">2.2.2 Adjusting Peaks in the Spectrum View</h5>
          <a href='#content0'>To Contents</a><br/>
            <p>
            <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Adjusting Peaks in the Spectrum View<br/>
            There a two ways to adjust the peak to a specific value, that is, 
            a specified mass position in the Spectrum View. 
            This topic describes how to adjust a peak using the <b>Shift Left</b> and 
            <b>Shift Right</b> commands and the Set Mass command in the DFS SmartTune software.<br/>
            
            <b><u>Shifting Peaks</u></b><br/>

            Click <b>Shift Left</b> to move the displayed mass range towards the left 
            and click <b>Shift Right</b> to move the displayed mass range towards the right; 
            the peak is moved in the Spectrum View.<br/>
            
            <u>Example of peak shifted in Spectrum View</u>  <br/>
            <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_20.png`} width="auto"  alt=''></img><br/>

            <b>Tip</b>  When you click the Shift Left/ Shift Right buttons, the magnetic 
            field strength is changed. <br/>

            <b>Set Mass Command (Electrical Locking)</b><br/>

            Another way to center the peak in the Spectrum View window is to change 
            the accelerating voltage (Ua).<br/>
            According to the equation 
            <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_17.png`} width="auto"  alt=''></img>
             it is possible to apply an electrical correction 
            factor (ECORR value) to the accelerating voltage and thus get the peak 
            adjusted to a defined mass position.<br/>
            
            <b>*  To adjust a peak in the middle of the Spectrum View using the electrical locking</b> <br/>

            1.  Click the <b>Define Scan</b> button to display the Define Scan dialog box. <br/>
            2.  Enter the mass number in the Magnetic Mass field (m/z = 313.98, for example). 
            If required, select a larger sweep width (0.5%, for example) to ensure that the 
            peak is found in the window. <br/>
            3.  Click <b>Apply</b> to display the new mass in the Spectrum View (without exiting 
            the Define Scan dialog box). <br/>
            
            <b>Tip</b>  The ECORR value is still set to the default value “1.” See ECORR Value. <br/>
            
            4.  Click the <b>Lock Mass</b> button in the toolbar to lock the selected mass. 
            The software will automatically adjust the ECORR value. <br/>
            
            Lock Mass 313.98 and ECORR readback  <br/>
            <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_19.png`} width="auto"  alt=''></img><br/>
            
            5.  Reduce the sweep width in the Define Scan dialog box (for example, to 0.2%) 
            and click <b>OK</b> to apply the selection. The Spectrum View will now display the exact 
            mass centered in the window. <br/>

            <b>Tip</b>  This operation is normally used for assigning a peak to a (correct) 
            mass value if there is a slight drift in the magnet calibration. <br/>

            6.  Click the <b> UnLock Mass</b> button to unlock the mass again and to reset 
            the ECORR value to the default value 1. <br/>

            <b>ECORR Value</b><br/>
            
            When you click the <b>Lock Mass</b> button, the so-called ECORR value 
            (Electric Correction factor) will be established. The ECORR readback 
            is displayed in the All page of the Status View. <br/>
            Its default value is 1. After an electrical lock (<b>Lock Mass</b> button activated), 
            ECORR will be larger than 1 if there was a shift to higher masses before and vice versa.<br/>
            </p>

			  <hr className='class-2'/>
        <h4 id="content023">2.3 Setting up DFS SmartTune</h4>
        <a href='#content0'>To Contents</a><br/>
          <p>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Setting up DFS SmartTune<br/>
          After finding some peaks, you can prepare for tuning the ion source.<br/>
          Before you start the tune procedure, pay attention to the information in the topics that follow.<br/>
          
          <b>Multiplier (SEM) Voltage</b><br/>
          
          Make sure that the multiplier voltage in the Detection Settings dialog box 
          is set to the value for a gain of 10<sup>6</sup>.<br/>
          
          <b>Tip</b>  After installation of the instrument, 
          the Thermo Fisher Scientific field service engineer will tell 
          you the multiplier voltage that corresponds to a multiplier gain of 10<sup>6</sup>. 
          However, you should check the multiplier gain from time to time. <br/>

          <b>Prolonging the SEM Lifetime</b><br/>

          <b>IMPORTANT</b>  The lifetime of an overloaded multiplier can become 
          very short if you use intense signals for tuning the ion source! Therefore, 
          do not use the ions listed in this table for tuning the ion source in low resolution. <br/>
          
          
          <table className='table  table-bordered table-striped'>
            <tbody>
              <tr className='table-info'>
                <td>m/z</td>
                <td>Ion</td>
              </tr>
              <tr>
                <td>18 </td>
                <td>Water</td>
              </tr>
              <tr>
                <td>28 </td>
                <td>Air (N2)</td>
              </tr>
              <tr>
                <td>32 </td>
                <td>Air (O2)</td>
              </tr>
              <tr>
                <td>69 </td>
                <td>base peak from PFK</td>
              </tr>
              <tr>
                <td>18, 35 </td>
                <td>CI reagent gas ions of ammonia </td>
              </tr>
              <tr>
                <td>29, 41 </td>
                <td>CI reagent gas ions of methane</td>
              </tr>
              <tr>
                <td>41, 58 </td>
                <td>CI reagent gas ions of isobutane</td>
              </tr>
              <tr>
                <td>93 </td>
                <td>[M+H]+ of glycerol (FAB)</td>
              </tr>
              <tr>
                <td>133 </td>
                <td>Cs+ (FAB)</td>
              </tr>
            </tbody>
          </table>

          
          <b>Tip</b>  These ions should only be used for getting started in case of extremely poor sensitivity. 
          Switch over to other ions as soon as possible (that is, after the first AUTOTUNE cycle)! <br/>

          <b>Sweep Mass for Tuning</b><br/>

          Select a sweep mass (for example, background, CI reagent gas, reference compound etc.), 
          which has an intensity of approximately 0.5 to 4.0 Volt (500 000 to 4 000 000 µV).<br/>
          
          <b>Parameters in a Tune File</b><br/>
          The following parameters set in the DFS SmartTune software are not saved in the tune file:<br/>
          •  Slit voltages <br/>
          •  Parameters from the Define Scan dialog box <br/>
          Every ten seconds, these parameters are saved in a binary file. When you start the DFS SmartTune software, this file is loaded after the tune file.<br/>
          </p>

			  <hr className='class-2'/>
        <h4 id="content024">2.4 Autotuning the DFS Mass Spectrometer</h4>
        <a href='#content0'>To Contents</a><br/>
          <p>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Autotuning the DFS Mass Spectrometer<br/>
          
          The tuning procedure requires that you introduce a tuning solution, or a solution of your analyte of interest, into the mass spectrometer at a steady rate for several minutes (or longer) at a time. The following example is based on the use of PFK as a reference compound. Depending on the type of application, different reference compounds can be used.<br/>
          
          <b>Optimizing the Source Focusing Parameters</b><br/>
          
          <b>*  To optimize the source focusing parameters</b> <br/>
          
          1.  If required, click the <b>Standby</b> button in the Control/Scan Mode toolbar to 
          turn on the mass spectrometer.  <br/>
          2.  When the operating condition is reached, the <b>Standby</b> button changes to the 
          green <b>On</b> button. Ions can now be displayed in the Spectrum View of the Tune window. <br/>
          3.  Before you start the Tune Sweep, make sure that the instrument is set to Low Resolution. 
          Click the <b>R</b> button to display the current resolution. If required, change the entrance 
          slit and exit slit voltages to set the correct resolution of R = 1000. <br/>
          
          <b>Autotuning Procedure</b><br/>
          
          <b>*  To perform an autotuning</b> <br/>
          
          1.  Insert a reference compound through the reference inlet system, 
          for example 1 µL PFK or FC43 with a 10 µL GC syringe. <br/>
          2.  Click the <b>Define Scan</b> button to display the Define Scan dialog box. <br/>
          a.  If required, click the <b>Scan</b> tab to display the Scan page. <br/>
          b.  Set the Scan mode to Tune Sweep. <br/>
          c.  Select an appropriate mass (for example, m/z = 331 for PFK) and sweep 
          width (for example, 0.5%) for basic tuning in low resolution. <br/>
          
          The Magnetic Mass m/z indicates the target mass of the magnet and Sweep 
          Width [%] indicates the scale of the spectrum display. <br/>
          
          <b>Tip</b>  Please note the list of forbidden ions for ion source tuning. See this table. <br/>
          
          d.  Click Apply to set the selected values and to close the Define Scan dialog box.  <br/>
          
          You should be able to see ions in the Spectrum View. If there are no ions, 
          you must check the instrument settings again. See Troubleshooting: No Ions. <br/>
          
          <b>Tip</b>  Start the Autotune procedure only if ions are visible in the Spectrum View. <br/>
          
          3.  Click the <b>AutoTune</b> button to display the AutoTune dialog box. 
          If necessary, click the <b>Automatic</b> tab to display the Automatic page. 
          By default, the Base Peak option button is selected. <br/>
          4.  Click <b>Start</b> to start the Autotune procedure, which will optimize 
          the lens parameters to improve the intensities.  <br/>
          You can observe the peak intensity directly in the Spectrum View or 
          in the Graph View according to the parameter change. In addition, 
          the progress of the procedure is displayed in the Status area.<br/>
          5.  In addition, you can also observe the tuning process 
          in the <b>Source Focusing</b> Settings dialog box. Click the Source Focusing button 
          to open the dialog box. <br/>
          
          The changes in the lens potentials can be seen in the readback values. The values in the spin boxes will be updated when the tuning procedure is completed.<br/>
          
          The Autotune procedure recognizes the ionization mode and activates the sub-procedures that are necessary to optimize this ionization mode. For example, in CI mode, pusher and the first draw lenses are not active. The autotune procedure will set the two values to zero and start tuning from the shield plates.<br/>
          In EI mode, all lenses will be optimized. The lens sequence is as follows:<br/>
          
          a.  Extraction <br/>
          b.  Pusher <br/>
          c.  Extraction <br/>
          d.  Pusher <br/>
          e.  L1-lens <br/>
          f.  X-lens <br/>
          g.  L2-lens <br/>
          h.  Y-lens <br/>
          i.  URATIO <br/>
          
          The end of the Autotune procedure is indicated in the Status area of the AutoTune dialog box.<br/>

          AutoTune dialog box, showing the Status area  <br/>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_23.png`} width="auto"  alt=''></img><br/>

          6.  After the run of the Autotune procedure is finished, check the signal intensity again: <br/>
          •  If the signal intensity was not improved after autotuning is completed, 
          the optimum values have been reached already and the tuning procedure is completed. <br/>
          Click <b>Stop</b> to close the AutoTune dialog box. <br/>
          •  If the signal intensity was improved, check whether the signal can be 
          further improved by restarting the autotune procedure. Go back to step 3. <br/>
          Tip  When you start from default values, it might take 5 to 8 cycles to 
          achieve optimal tuning (that is, optimum intensity). <br/>
          7.  After you have reached the best conditions, save the settings by 
          either clicking <b>Save</b> to replace the existing file or by choosing File &gt; Save As 
          to save the file under a new name. <br/>
          
          <b>Tip</b>  The parameters Entrance Slit and Exit Slit, which are set 
          in the DFS SmartTune software, are not saved in the tune file. <br/>
          </p>

			  <hr className='class-2'/>
        <h4 id="content025">2.5 Semi-Automatic Tuning</h4>
        <a href='#content0'>To Contents</a><br/>
          <p>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Semi-Automatic Tuning<br/>
          Use the Semi-Automatic page of the AutoTune dialog box to optimize automatically 
          one tune parameter at a time for a specified mass peak of interest. 
          The tuning procedure requires that you introduce tuning solution, 
          or a solution of your analyte of interest, into the mass spectrometer 
          at a steady rate for several minutes (or longer) at a time. 
          You can introduce the solution directly from the syringe pump or 
          from the syringe pump into the effluent of the LC by using a Tee union.<br/>
          Semi-Automatic Tuning of Individual Parameters<br/>
          The initial steps for semi-automatic tuning are exactly the same as described 
          for the automatic tuning procedure.<br/>

          <b>*  To tune a specific parameter of the DFS mass spectrometer</b> <br/>

          1.  Click the <b>AutoTune</b> button to display the AutoTune dialog box.  <br/>
          2.  If necessary, click the <b>Semi-Automatic</b> tab to display the Semi-Automatic tuning page. <br/>
          3.  Select the correct item from the list box in the What To Optimize area. <br/>
          <b>Tip</b>  When you start the tuning procedure for a lens, both the lens potential 
          as well as the lens symmetry are optimized, where applicable. <br/>
          4.  Enter the Optimization Range values for the Start and End value and define the tuning step. <br/>
          5.  Click <b>Start</b> to start the Autotune procedure for the selected parameters.  <br/>

          While semi-automatic tuning is in progress, the changing ion intensity 
          can be observed in the Spectrum View and in the Source Focusing dialog box. 
          In addition, the progress of the procedure is displayed in the Status area.<br/>
          The end of the procedure is indicated in the Status area of the AutoTune dialog box.<br/>
          The new settings will also be displayed in the Semi-Automatic page.<br/>
          
          <b>Results group of the Semi-Automatic page</b>  <br/>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_25.png`} width="auto"  alt=''></img><br/>

          The Initial Setting indicates the parameter value set before the 
          semi-automatic tuning procedure was started and the Best Setting 
          displays the new, optimized parameter value.<br/>
          6.  After you have reached the best conditions, save the settings 
          by either clicking Save to replace the existing file or by choosing File &gr; Save As 
          to save the file under a new name. <br/>

          </p>
          
			  <hr className='class-2'/>
        <h4 id="content026">2.6 Manual Tuning</h4>
        <a href='#content0'>To Contents</a><br/>
          <p>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Manual Tuning<br/>
          The DFS SmartTune software offers two ways for manually tuning the instrument:<br/>
          •  By using the parameter dialog boxes of the Instrument Control bar 
          (for example, the Source Focusing dialog box) <br/>
          •  By using the Manual page of the AutoTune dialog box. <br/>

          <b>Tuning by Parameter Dialog Boxes</b><br/>

          You can manually tune the instrument parameters in the various dialog boxes 
          of the Instrument Control bar. Open the desired set of parameters by clicking 
          the buttons in the Instrument Control bar or by selecting the individual 
          menu items from the Setup menu.<br/>

          <b>Tuning Ion Source Parameters</b><br/>

          <b>*  To tune the ion source parameters</b> <br/>

          1.  Click the <b>Ion Optics</b> button or choose <b>Setup &gt; Ion Optics</b> to open 
          the Ion Optics Settings dialog box. <br/>
          2.  Select the parameter you want to optimize by clicking the 
          respective option button. You can then change the currently set parameter 
          by using one of the following methods: <br/>
          •  Enter a new value in the cell using the keyboard. <br/>
          •  Adjust the value by clicking the Up and Down arrows next to the cell. <br/>
          •  Use the slider at the bottom of the dialog box, which will also 
          indicate the lowest and highest values possible. <br/>

          The new value will be set by the software and the Spectrum View/Graph View 
          will display the changed intensity/signal.<br/>
          
          <b>Tip</b>  The edited values will only be saved when you confirm the changes 
          by clicking <b>OK</b> after tuning is completed! <br/>

          3.  After you have reached the best conditions (maximum intensity), 
          save the settings by either clicking Save to replace the existing file 
          or by choosing File &gt; Save As to save the file under a new name. <br/>

          </p>
			  
        <hr className='class-2'/>
        <h4 id="content027">2.7 Troubleshooting: No Ions</h4>
        <a href='#content0'>To Contents</a><br/>
          <p>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Troubleshooting: No Ions<br/>
          If the selected mass is missing and no ions can be seen after the previous operations, 
          this may be due to the reasons shown in this table.<br/>


          <b>No Ions—Troubleshooting </b> 
          <table className='table  table-bordered table-striped'>
            <tbody>
              <tr className='table-info'>
                <td>Problem</td>
                <td>Checks</td>
              </tr>
              <tr>
                <td>Ion Volume</td>
                <td>Make sure that the ion volume is inserted.</td>
              </tr>
              <tr>
                <td>Accelerating Voltage OFF</td>
                <td>Make sure that the accelerating voltage is switched on in the Source Focusing Settings dialog box.</td>
              </tr>
              <tr>
                <td>Conversion Dynode OFF</td>
                <td>Make sure that the conversion dynode is switched on in the Detection Settings dialog box.</td>
              </tr>
              <tr>
                <td>Multiplier Voltage</td>
                <td>Check the multiplier voltage in the Detection Settings dialog box.</td>
              </tr>
              <tr>
                <td>Wrong Polarity</td>
                <td>Toggle the polarity switch from negative to positive mode.</td>
              </tr>
              <tr>
                <td>Incorrect Setting of URATIO</td>
                <td>Change the URATIO value in the Ion Optics Settings dialog box (1.6 -&gt; 2.4%).</td>
              </tr>
              <tr>
                <td>Incorrect Electron Energy</td>
                <td>Check the electron energy values in the Ion Source Settings dialog box (set to default: 70 eV).</td>
              </tr>
              <tr>
                <td>Entrance or Exit Slit closed</td>
                <td>Make sure that the entrance slit and the exit slit are open;
                  set the voltages to the highest values in the Ion Optics Settings dialog box.</td>
              </tr>
              <tr>
                <td>Wrong sweep width </td>
                <td>Change the sweep width in the Define Scan dialog box (for example, to 5%) and restart a scan.</td>
              </tr>
              <tr>
                <td>ERATIO</td>
                <td>Check the ERATIO value in the Source Focusing dialog box: 
                  if it is different from ‘1’, set to Eratio = 1.</td>
              </tr>
              <tr>
                <td>Filament Current/ Emission Current</td>
                <td>Check the filament current and the emission current in the Ion Source Settings dialog box.</td>
              </tr>
            </tbody>
          </table>
          </p>
			  
        <hr className='class-2'/>
        <h4 id="content028">2.8 Starting a New Scan</h4>
        <a href='#content0'>To Contents</a><br/>
          <p>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Starting a New Scan<br/>
          If still no ions are visible, start a new scan to check if ions are detected but the 
          mass calibration is off.<br/>
          
          <b>*  To start a new scan to correct the mass calibration</b> <br/>

          1.  Click the <b>Define Scan</b> button to display the Define Scan dialog box. <br/>
          a.  Select the Scan Mode Magnetic Scan and set the following parameters: <br/>

          First Mass m/z = 2<br/>
          Last Mass m/z = 700<br/>
          Scan rate =  2<br/>

          <b>Define Scan dialog box with new parameters</b>  <br/>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_28.png`} width="auto"  alt=''></img><br/>
          b.  In the Define Scan dialog box, click <b>Apply</b> to start a scan again.  <br/>


          <b>Tip</b>  If signals with strange mass assignments are displayed, 
          then there are ions but the mass spectrometers mass scale is not correctly calibrated. 
          This can happen when the operator uses only individual names for saving calibration tables. 
          The STARTUP procedure always recalls MCAL1.CAL. Save an actual calibration not only with 
          an individual name, but also as MCAL1.CAL! <br/>

          2.  Correct the mass calibration: <br/>
          •  Either restore the last valid calibration file by choosing File &gt; Restore Factory Calibration. <br/>
          •  Or carry out a new mass calibration. <br/>

          3.  Start the scan once more. The system will apply the new calibration. <br/>

          </p>

		</div>


  )


}
