import React from 'react';

function DFS_OM_ko_3() {
	return (
		<>
			<div className="px-4" style={{marginTop:30}}>
					<MainContents />
			</div>
		</>
	);
};
export default DFS_OM_ko_3
function MainContents() {
	const title = 'Chapter 3. Direct Evaporation System';


	return (
		<div>
			<h1 className='title'>{title}</h1>	
		
			<hr className='class-1'/>
			<h2>1. Direct Evaporation System(직접 증발 시스템)</h2>
        <p>
        목차<br/>
        • 직접 증발 시스템 소개 <br/>
        • DI 프로브 <br/>
        • DCI 프로브<br/>
        • DIP-HT 장치가 있는 DCI 프로브 <br/>
        </p>

        <hr className='class-2'/>
				<h4>1.1 직접 증발 시스템 소개 </h4>
          <p>
          고체 시료의 직접 증발을 위해 세 가지 유형의 시료 프로브를 사
          용할 수 있습니다.<br/>
          • DI 프로브 - 도가니에서 시료를 증발시키기 위한 프로브이
          며 1단 냉각 장치가 있습니다. 획득 가능한 최대 증발 온도
          는 약 360°C이며 가열 속도는 20°C에서 200°C로 30초,
          200°C에서 300°C로 30초입니다. 자세한 내용은 3-6 페
          이지를 참조하십시오.<br/>
          • DCI 프로브(선택 사항) - 가열된 와이어에서 신속하게 시료
          를 증발시키기 위한 프로브입니다. 획득 가능한 최대 증발 온
          도는 약 1,600°C이며 가열 속도는 500°C/초를 초과합니다.
          이 프로브에는 냉각 장치가 제공되지 않습니다. 자세한 내용
          은 3-9 페이지를 참조하십시오.<br/>
          • DIP-HT 팁(선택 사항) - DCI 프로브에 장착합니다. 이 버전
          은 증발 온도가 최대 800°C인 도가니에서 증발시키기 위한
          것입니다. 이 프로브에는 냉각 장치가 제공되지 않습니다.
          자세한 내용은 3-11 페이지를 참조하십시오.<br/>
          DFS Tune 소프트웨어로 직접 증발 모드와 매개변수를 설정하고
          확인할 수 있습니다. 직접 증발 프로브를 사용하는 측정 수행에
          대한 자세한 내용은 7-46 페이지의 “고체 시료의 직접 증발”을
          참조하십시오.
          </p>

        <hr className='class-2'/>
				<h4>1.2 Vacuum Lock for Direct Probes(직접 프로브용 진공 잠금 장치)</h4>
          <p>
          시료 프로브의 팁은 진공 잠금 장치를 통해 소스 헤드로 삽입된
          다음 이온 소스의 이온화 volume에 매우 근접하게 됩니다. 직접 증
          발 장치용 진공 잠금 장치는 소스 헤드의 왼쪽에 있습니다.
          그림 3-1을 참조하십시오.<br/>
          이 장치는 공압식으로 작동되며 시스템 제어판의 Exchange
          Locks Probe(잠금 프로브 교환) 버튼에 의해 제어됩니다.
          2-7 페이지의 그림 2-4를 참조하십시오. 직접 증발 장치의 진
          공 잠금 장치는 이온화 박스 교환 장치의 진공 잠금 장치와 매우
          유사합니다. 2-55 페이지의 그림 2-44를 참조하십시오.
          </p>
          <img  src={`${process.env.PUBLIC_URL}/images/DFS_OM_KO/image074.png`} width="auto"  alt=''></img><br/>
          그림<b> 3-1 </b>직접 프로브용 진공 잠금 장치

          <p>프로브 대신 O링과 캡 스크루가 있는 실링 플러그(블라인드 인
          서트)를 잠금 장치에 삽입하여 잠금 volume(lock volume)를 공
          기와 분리할 수 있습니다. 그림 3-2는 DI 잠금 장치의 측면 구
          성도를 보여줍니다.</p>

          <img src={`${process.env.PUBLIC_URL}/images/DFS_OM_KO/image075.png`} width="auto"  alt=''></img><br/>
          그림 3-2 직접 프로브 잠금 장치의 측면 구성도

          <p>압축 공기 작동식 슬라이더 밸브는 잠금 volume와 소스 헤드의 volume
          를 분리합니다. 잠금 volume는 소스 헤드로 유입되기 전에 직접 증
          발 프로브의 팁을 둘러쌉니다. 잠금 volume는 슬라이더 밸브로 한쪽
          끝을 이루고 반대쪽은 프로브 중 하나 또는 위에서 언급한 실링 플
          러그에 의해 닫힐 수 있습니다. 잠금 volume는 저진공
          (forevacuum) 밸브를 통해 주입되고 배출 밸브를 통해 배출됩니
          다.
          직접 프로브 잠금 장치에는 보호 수단으로 라이트 배리어가 있
          습니다. 로드가 라이트 배리어를 통과할 때 슬라이더는 튜브 안
          으로 위를 향해 이동하거나 튜브 밖으로 아래를 향해 이동할 수
          없습니다. 따라서 슬라이더가 프로브 로드를 누를 수 없습니다.
          직접 증발 프로브를 삽입하고 작동하는 동안 실링 부시가 진공
          기밀성을 제공합니다. 실링 부시에는 실링 인서트의 뒷면에서
          압축된 실링 링이 있어 프로브 로드의 연마된 표면을 단단히 둘
          러싸고 있습니다. 실링 인서트는 캡 스크루로 진공 잠금 장치에
          연결됩니다. 실링 인서트의 전면에 있는 Viton™ O링이 실링 인
          서트와 진공 잠금 장치 사이를 밀폐합니다.
          직접 증발 프로브(3-7 페이지의 그림 3-7 참조)의 로드에 스
          크루로 고정된 캡 너트는 실링 부시에서 로드 이탈을 방지합니
          다. 프로브 로드를 뒤로 끌면 캡 너트에 의해 끝 위치에서 멈춥
          니다. 실링 인서트에서 로드를 제거하기 전에 캡 너트를 제거
          하십시오. 또한 이 캡 너트를 사용하여 전체 프로브 팁을 교체
          할 수 있습니다.</p>

          <img src={`${process.env.PUBLIC_URL}/images/DFS_OM_KO/image006.jpg`} width="auto"  alt=''></img><br/>
          <p>진공 잠금 장치를 장시간 열어 두지 마십시오. 정전 시 슬라이
          더 밸브가 열립니다. 갑자기 공기가 유입되면 필라멘트와 소
          스 TMP가 손상될 수 있습니다.
          프로브 로드가 삽입되지 않은 경우 블라인드 인서트를 사용하
          여 진공 잠금 장치를 닫아 둡니다.</p>
          <p>프로브 잠금 장치의 사용 지침은 7-46 페이지의 “고체 시료의
          직접 증발”을 참조하십시오.<br/>
          더 많은 프로브를 사용하거나 수리 후 프로브 로드의 작동 위치
          를 조정해야 합니다. Thermo Fisher Scientific 현장 서비스 엔
          지니어를 부르십시오.</p>

        <hr className='class-1'/>
			<h2>2. Electric Connections of the Sample Probes </h2>
        <p>주사(scan) 장비 제어 보드로 제어되는 프로브 히터 공급 보드
        에서 프로브의 가열 전류를 공급합니다.
        프로브의 전원 코드는 소스 헤드의 양쪽에 있는 피드스루 중 하
        나를 통해 연결됩니다. 접지선은 프로브 잠금 장치 플랜지 아래
        의 개구부를 통해 소스 헤드로 연결됩니다. 3-5 페이지의 그
        림 3-3에서는 삽입된 DI 프로브와 연결된 모든 공급 장치를 보
        여줍니다.</p>

        <img src={`${process.env.PUBLIC_URL}/images/DFS_OM_KO/image076.png`} width="auto"  alt=''></img><br/>
        그림 3-3 DI 프로브와 연결된 공급 장치

        <h5>프로브 유형 인식</h5>
        <p>DI 및 DCI 프로브의 전기 플러그는 장비 컨트롤러가 연결된 프
          로브의 유형을 인식하는 방식으로 코딩됩니다. HT 팁이 있는 DCI 
          프로브는 플러그에서 인식할 수 없습니다. 이 버전을 사용 하는 경우 
          DFS Tune 소프트웨어에서 DIP-HT 모드를 수동으 로 정의해야 합니다. 
          프로브의 성공적인 연결이 소프트웨어에 표시됩니다.</p>
        <p><i>DFS</i>소프트웨어<i> </i>설명서에서는 DFS Tune 소프트웨어에서 프 
        로브를 구성하는 방법(예: 온도계 그래프의 온도 눈금 설정) 에 대한 자세한 정보를 제공합니다.</p>


      <hr className='class-2'/>
				<h4>2.1 Support Clamp(지지 클램프) </h4>
          <p>3개의 개구부가 있는 지지 클램프가 장비의 왼쪽에 장착됩니다.
          그림 3-4를 참조하십시오. 장비 왼쪽에 GC 테이블이 설치된 경
          우 지지 클램프가 이 GC 테이블 측면에 장착됩니다. 여기에서 프
          로브 로드를 사용하지 않을 때 내려 놓을 수 있습니다. 공급 라인
          을 분리할 필요가 없습니다. 오른쪽 개구부는 다른 2개보다 큽
          니다. 박스 교환 로드를 사용하지 않을 때 여기에 보관하십시오.</p>

          <img src={`${process.env.PUBLIC_URL}/images/DFS_OM_KO/image077.jpg`} width="auto"  alt=''></img><br/>
          그림 3-4 프로브 로드 및 박스 교환 로드용 지지 클램프

      <hr className='class-1'/>
			<h2>3. DI Probe</h2>
        <p>
        DI 프로브의 작동 원리는 다음과 같습니다. 히터, 냉각 회로 및
        온도 프로브가 조절 장치를 구성합니다. 이 장치를 사용하면 개
        별 온도 값을 설정하거나 특정 온도 추이를 프로그래밍할 수 있
        습니다.<br/>
        DI 프로브에는 저전압 측 프로브의 로드 섹션에 히터, 냉각기 및
        온도 프로브가 포함되어 있습니다. 열은 단열재에 의해 프로브
        팁에 있는 도가니 오븐에 전달됩니다. 작동 위치에서 프로브 팁
        은 이온 소스에 닿으므로 5kV 가속 전압으로 설정됩니다. 히터
        는 조절 회로의 훨씬 낮은 전위에 있고 냉각기는 접지 전위에 있
        습니다. 이는 훨씬 더 경제적인 구조로 이어지지만 조절 속도와
        정밀도가 감소됩니다.
        </p>
        <img rc={`${process.env.PUBLIC_URL}/images/DFS_OM_KO/image078.jpg`} width="auto"  alt=''></img><br/>
        그림 3-5 DI 프로브

        <p>시료는 도가니에 보관됩니다. 시료가 프로브 팁의 전면에 삽입
        되고 클램프로 고정됩니다. 그림 3-6을 참조하십시오.</p>

        <img src={`${process.env.PUBLIC_URL}/images/DFS_OM_KO/image079.png`} width="auto"  alt=''></img><br/>
        그림 3-6 도가니 삽입

        <p>작동 중 도가니 오븐은 이온 소스 블록의 주입 구멍에 삽입되고
        도가니의 앞쪽 끝은 이온화 영역 앞에 위치합니다. 프로브 팁
        의 실링 표면이 이온 소스 블록의 외부 실링 실린더에 밀착됩니
        다. (2-38 페이지의 “이온 소스 및 소스 공급 장치”도 참조
        하십시오.) 따라서 라인이 밀폐되어 EI 및 CI 작동에 직접 프로
        브를 사용할 수 있습니다.</p>

        <img  src={`${process.env.PUBLIC_URL}/images/DFS_OM_KO/image080.png`} width="auto"  alt=''></img><br/>
        그림 3-7 DIP 팁의 구성도

        <h5>사용 가능한 도가니 유형</h5>
        <p>표 3-1에는 DI/DCI 프로브에 사용할 수 있는 도가니 유형이 나
        열되어 있습니다.</p>

        표 3-1 DIP/DCI 프로브에 사용 가능한 도가니<br/>
        <table className='table  table-bordered table-striped'>
          <tbody>
            <tr className='table-info'>
              <td><b>#</b></td>
              <td>도가니<b>
              </b>유형</td>
              <td>설명</td>
            </tr>
            <tr>
              <td>1.</td>
              <td>알루미늄 도가니</td>
              <td>고체 시료용입니다. 그림 3-8의 왼쪽을 참조하십시오.</td>
            </tr>
            <tr>
              <td>&nbsp;</td>
              <td>구성된 알루미늄 도가니:</td>
              <td>액체 시료용입니다.</td>
            </tr>
            <tr>
              <td>2. 3.</td>
              <td>도가니 도가니 홀더</td>
              <td>이 도가니 유형은 도가니와 도가니 홀더에서 사용자가 조립해 야 합니다. 그림 3-8의 오른쪽을 참조하십시오.</td>
            </tr>
            <tr>
              <td>4.</td>
              <td>석영 도가니</td>
              <td>DIP-HT 장치에 사용됩니다. 자세한 내용은 3-11 페이지를 참조하십시오.</td>
            </tr>
          </tbody>
        </table>

        <img src={`${process.env.PUBLIC_URL}/images/DFS_OM_KO/image081.png`} width="auto"  alt=''></img><br/>
        그림 3-8 DI 프로브 팁용 도가니
        <p>일반적으로 알루미늄 도가니가 사용됩니다. 도가니는 사용 후
        폐기됩니다. 도포하기 전에 세척하는 것이 좋습니다. 아세톤의
        초음파 세척기에서 여러 번 끓이고 조심스럽게 건조한 다음 유
        리 용기에 보관하십시오.</p>


        <hr className='class-2'/>
				<h4>3.1 Cooling of the DI Probe </h4>
          <p>냉각수 회로에서 DI 프로브의 냉각을 제공합니다. DI 프로브의
          냉각수용 파란색 플라스틱 호스는 특수 플러그로 끝납니다. 이
          플러그는 장비의 일반 냉각수 회로의 해당 소켓에 삽입됩니다.
          이 소켓은 GC 테이블 아래의 소스 헤드의 왼쪽과 오른쪽에 있습
          니다. 3-5 페이지의 그림 3-3을 참조하십시오. 자세한 내용은
          2-74 페이지의 “냉각수 회로”를 참조하십시오.</p>


      <hr className='class-1'/>
			<h2>4. DCI Probe </h2>
        <p>DCI(직접 화학적 이온화) 직접 증발 프로브는 원래 화학적 이온
        화 모드에서 순간적 증발을 위해 고안되었지만 EI(전자 충격) 이
        온화 모드에서 순간적 증발에도 사용할 수 있습니다. 대부분의 경
        우 매우 빠른 증발은 시료 분해를 방지합니다.<br/>
        DCI 프로브에는 절연 처리된 팁, 핸들이 있는 프로브 로드 및 실링
        부시가 있는 실링 인서트가 있습니다. 전기 공급 와이어는 핸들의
        소켓에서 로드를 통해 절연 처리된 팁으로 이어지고 증발 와이어
        장치용 소형 소켓에서 끝납니다. 그림 3-9를 참조하십시오.</p>

        <img src={`${process.env.PUBLIC_URL}/images/DFS_OM_KO/image082.png`} width="auto"  alt=''></img><br/>
        그림 3-9 DCI 프로브
        
        <img src={`${process.env.PUBLIC_URL}/images/DFS_OM_KO/image085.png`} width="auto"  alt=''></img><br/>
        그림 3-10 DCI 프로브 팁의 구성도

        <p>증발 와이어 장치(DCI 와이어 장치)는 프로브 팁 전면의 소켓
        에 삽입됩니다. 용해된 시료는 주사기에서 와이어 루프로 떨어
        집니다. 용매 증발 후 DCI 시료 프로브가 소스 블록에 삽입됩니
        다. DFS Tune 소프트웨어를 사용하여 온도 값을 설정하고 읽
        을 수 있으며, 온도 프로그램을 편집, 시작, 중지 및 중단할 수
        있습니다. 자세한 내용은 DFS 소프트웨어 설명서를 참조하십
        시오. DCI 프로브는 냉각되지 않습니다.<br/>
        작동 중 DCI 프로브의 팁은 이온 소스 블록의 주입 구멍에 삽입
        되고 와이어 장치의 전단은 이온화 영역 앞에 위치합니다. 제거
        가능한 프로브 팁의 전면에 있는 실링 표면은 이온 소스 블록의
        외부 실링 실린더에 밀착됩니다. (2-38 페이지의 “이온 소스
        및 소스 공급 장치”도 참조하십시오.) 따라서 라인이 밀폐되어
        EI 및 CI 작동에 직접 프로브를 사용할 수 있습니다.<br/>
        팁 이온화 박스를 교체하기 전에 프로브를 잠그십시오.<br/>
        작동 중 직접 프로브의 절연 처리된 팁은 이온 소스와 접촉합니
        다. 따라서 프로브가 이온 소스에 완전히 삽입되면 팁이 5kV 가
        속 전압에 연결됩니다.</p>

      <hr className='class-1'/>
			<h2>5. DCI Probe with DIP-HT Device(DIP-HT 장치가 있는 DCI 프로브)</h2>
        <p>DIP-HT 장치는 DCI 시료 프로브의 옵션 액세서리로서 DCI 프
        로브 팁과 작은 내부 세라믹 튜브를 대체합니다. 전면의
        DIP-HT 팁에는 와이어 클램프가 있는 작은 세라믹 오븐이 있습
        니다. 시료는 특수 석영 도가니에 보관됩니다. 그런 다음 도가
        니를 오븐의 전면 구멍에 삽입하고 클램프로 고정합니다. 그
        림 3-11과 그림 3-12를 참조하십시오.</p>
        <img src={`${process.env.PUBLIC_URL}/images/DFS_OM_KO/image084.png`} width="auto"  alt=''></img><br/>
        그림 3-11 DIP-HT 팁

        <p>DIP-HT 팁이 있는 DCI 프로브는 다른 모든 프로브의 팁보다 깁
        니다. 따라서 올바른 작동 위치에서 프로브를 정지하려면 플라
        스틱 클램프를 로드에 고정해야 합니다. 자세한 내용은
        7-65 페이지를 참조하십시오.</p>
        <img src={`${process.env.PUBLIC_URL}/images/DFS_OM_KO/image085.png`} width="auto"  alt=''></img><br/>
        그림 3-12 DIP-HT 팁이 있는 DCI 프로브의 구성도

        <p>DIP-HT 도가니의 최대 온도는 약 800°C입니다. 주위 온도에
        서 700°C까지 가열하는 데 90초 가량 걸립니다. 이 프로브에
        는 냉각 장치가 제공되지 않습니다.</p>

		</div>


  )
}