import React from 'react';

function DFS_Help_3() {
	return (
		<>
			<div className="px-4" style={{marginTop:30}}>
					<MainContents />
			</div>
		</>
	);
};
export default DFS_Help_3
function MainContents() {
	const title = 'Calibrating the Mass Spectrometer';

	return (
		<div>
			<h1 className='title' id="content0">{title}</h1>	
			<hr className='class-1'/>
      <h5>Contents</h5>
      <p>
      <a href='#content003'>3. Calibrating the Mass Spectrometer</a><br/>
      <a href='#content031'>3.1 Calibrating the Instrument</a><br/>
      <a href='#content311'>  3.1.1 Preconditions for Calibration</a><br/>
      <a href='#content312'>  3.1.2 Semi-Automatic Calibration</a><br/>
      <a href='#content313'>  3.1.3 Run a Semi-Automatic Calibration</a><br/>
      <a href='#content314'>  3.1.4 Advanced Calibration</a><br/>
      <a href='#content315'>  3.1.5 Reference Tables</a><br/>
      <a href='#content032'>3.2 Multiplier Gain Determination</a><br/>
      <a href='#content033'>3.3 Resolution Check Procedure</a><br/>
      <a href='#content331'>  3.3.1 Report</a><br/>
      </p>
			<hr className='class-1'/>

      <h2 id="content003">3. Calibrating the Mass Spectrometer</h2>
      <a href='#content0'>To Contents</a><br/>
          <p>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Calibrating the Mass Spectrometer<br/>
          With a sector field instrument, scanning is the process of sequentially changing the strength of the magnetic field or the electric field in a single direction to detect ions with different masses. Magnetic scans and electric scans must be calibrated to obtain a correct mass indication.<br/>
          
          Contents<br/>
          •  Calibrating the Instrument <br/>
          •  Multiplier Gain Determination <br/>
          •  Resolution Check Procedure <br/>
          </p>
			  
        <hr className='class-2'/>
        <h4 id="content031">3.1 Calibrating the Instrument</h4>
        <a href='#content0'>To Contents</a><br/>
          <p>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Calibrating the Instrument<br/>
          To ensure optimum accuracy of the instrument, the DFS Magnetic Sector GC-HRMS system 
          must be calibrated for basic scan measurements. Accurate mass calibration leads to 
          a correct identification of the acquired ion mass peaks.<br/>
          <b>*  To check whether a calibration is necessary</b> <br/>

          Click the <b>Unlock</b> button in the toolbar. If the deviation of the displayed mass 
          is more than 0.3 mass units off, then you need to perform a mass calibration.<br/>
          Use the Calibrate dialog box to calibrate the instrument. This dialog box offers 
          semi-automatic calibration procedures and an advanced calibration procedure.<br/>
          The items to be calibrated can be selected from a list and then started from 
          the dialog pages. Upon completion of the calibration procedure(s), 
          the software displays a message that indicates whether the parameter(s) 
          are calibrated properly or not. Or, magnetic and electric calibration 
          can be carried out with the Advanced page of the Calibrate dialog box. <br/>

          </p>
          
          <hr className='class-3'/>
          <h5 id="content311">3.1.1 Preconditions for Calibration</h5>
          <a href='#content0'>To Contents</a><br/>
          <p>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Preconditions for Calibration<br/>
          To perform a calibration using the Semi-Automatic page of the Calibrate dialog box, 
          PFK must be used as a calibration solution. The calibration masses and 
          all experimental parameters like scan ranges, resolution settings, etc. 
          are set automatically and cannot be influenced by the user. 
          The calibration solution for the advanced calibration can be selected 
          by the user according to the supplied reference files.<br/>
          Before you start the calibration, make sure that the following conditions are met:<br/>

          •  The DFS mass spectrometer is in operating condition. <br/>

          The mass spectrometer should already be scanning for approximately 1/2 hour 
          or longer to obtain temperature stability for the field probe control of the magnet. 
          If the system has been calibrated while the magnet had a very low (parked at mass 2) 
          or very high (parked at mass 700) temperature, the calibration in normal scan mode 
          may drift a little after a couple of minutes due to temperature changes of the magnet.<br/>
          
          <b>Tip</b>  A mass spectrometer that is used mostly for scanning work should scan all the time. 
          Under those conditions, a calibration table will be valid for a long time. <br/>

          •  The reference compound (PFK for semi-automatic calibration) is injected. <br/>
          •  The instrument is scanning and the scan is displayed in the Spectrum View. <br/>

          <b>Spectrum View - Display Scan</b>  <br/>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_31.png`} width="auto"  alt=''></img><br/>

          •  The multiplier is set to the appropriate value. <br/>
          •  The intensity of m/z = 69 is around 30 000 000 [µV]. <br/>

          </p>
          
          <hr className='class-3'/>
          <h5 id="content312">3.1.2 Semi-Automatic Calibration</h5>
          <a href='#content0'>To Contents</a><br/>
          <p>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Semi-Automatic Calibration<br/>
          Use the Semi-Automatic page of the Calibrate dialog box to calibrate individual parameters, 
          for example only the Normal Scan Rate Types of the Mass Calibration.<br/>

          <b>*  To calibrate an individual parameter</b> <br/>

          Click the <b>Start</b> button next to the item. The calibration procedure for the selected item 
          is started and the calibration info will be updated after a certain time period. <br/>

          The Last Cal. Date readback column in the Semi-Automatic page gives the date 
          of the last successful calibration for each item. If a calibration fails, 
          the last successful calibration date still appears in the column. 
          The last successful calibration continues to be in effect in the instrument. 
          However, the result column will show a red ✗ mark to indicate that the currently 
          attempted calibration has failed or was aborted.<br/>

          </p>
          
          <hr className='class-3'/>
          <h5 id="content313">3.1.3 Run a Semi-Automatic Calibration</h5>
          <a href='#content0'>To Contents</a><br/>
          <p>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Run a Semi-Automatic Calibration<br/>
          <b>Tip</b>  Before you start a semi-automatic calibration, make sure that the prerequisite conditions 
          as described in Preconditions for Calibration are met. <br/>

          <b>*  To perform a semi-automatic calibration</b> <br/>

          1.  Click the <b>Calibrate</b> button to open the Calibrate dialog box. <br/>
          2.  Click the <b>Semi-Automatic</b> tab to display the Semi-Automatic page. <br/>

          Click the <b>Start</b> button next to the item that you want to calibrate, for example MDAC. 
          The calibration procedure is started.<br/>
          
          <b>Detail of semi-automatic calibration page</b>  <br/>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_34.png`} width="auto"  alt=''></img><br/>

          3.  Observe the DFS SmartTune window and the Calibrate dialog box. 
          While the calibration is in progress, the software displays the results 
          in the Graph View and in the Status box of the Calibrate dialog box. <br/>

          The calibration procedure typically takes only a few minutes.<br/>
          If the calibration has been successful (highest intensities on the 
          masses m/z = 69, m/z = 119, and m/z = 131), the calibrated item shows 
          a check mark () in the Results column. The Status box displays the message 
          “Calibration completed/successful.”<br/>
          If a calibration item fails, try calibrating on that item again.<br/>
          4.  Save the calibration. <br/>

          After the semi-automatic calibration is successfully finished, 
          you have to adjust the magnet calibration. In contrast to the semi-automatic 
          calibration that requires PFK as reference compound, you can use other reference 
          compounds (FC43, for example) with this adjusting procedure.<br/>

          <b>*  To adjust the active magnet calibration</b> <br/>

          1.  Perform an autotuning procedure with a reference compound of your 
          choosing as described in Getting Started. <br/>
          2.  Click the <b>Calibrate</b> button to open the Calibrate dialog box. <br/>
          3.  Click the <b>Calibration</b> tab to display the Calibration page. <br/>
          4.  Select the correct reference table file for the used reference 
          compound from the Reference list box in the Parameter area. <br/>
          5.  Click <b>Calibrate</b> to start the calibration. <br/>

          If the calibration adjustment has been successful, the Status box displays 
          the message “Calibration completed/successful.”<br/>

          </p>
          
          <hr className='class-3'/>
          <h5 id="content314">3.1.4 Advanced Calibration</h5>
          <a href='#content0'>To Contents</a><br/>
          <p>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Advanced Calibration<br/>
          Use the Advanced page of the Calibrate dialog box to perform a high resolution magnet 
          calibration or an electric calibration.<br/>

          <b>Manual Magnetic Calibration</b><br/>

          <b>Tip</b>  Before you start the manual magnetic calibration, make sure that the 
          prerequisite conditions described in Preconditions for Calibration are met. 
          However, for the manual calibration you can use a reference compound other than PFK. <br/>

          <b>*  To perform a manual magnetic calibration</b> <br/>

          1.  Click the <b>Calibrate</b> button to open the Calibrate dialog box. <br/>
          2.  Click the <b>Advanced</b> tab to display the Advanced page. <br/>
          3.  Select the correct reference table file for the used reference compound 
          from the list box in the Magnetic High Resolution area. <br/>
          4.  Click <b>Calibrate</b> to start the magnetic calibration. 
          The Graph View will plot a graph that shows the current calibration curve. <br/>

          <b>Graph View with calibration curve</b>  <br/>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_37.png`} width="auto"  alt=''></img><br/>

          5.  A popup dialog will appear on the screen asking whether the calibration is okay. 
          If the curves are smooth, click <b>Ok</b> and save the calibration. <br/>
          6.  The current scan is stopped, the software calculates a new calibration, 
          and a new scan is started. <br/>

          The magnetic calibration is valid until a new magnetic calibration is performed.<br/>
          
          <b>Manual Electric Calibration</b><br/>

          <b>*  To perform a manual electric calibration</b> <br/>

          1.  Make sure that the prerequisite conditions as mentioned in Manual Magnetic Calibration 
          are met before you start the calibration. <br/>
          2.  Click the <b>Calibrate</b> button to open the Calibrate dialog box. <br/>
          3.  Click the <b>Advanced</b> tab to display the Advanced page. <br/>
          4.  In the Electric area, enter the Reference Mass 1 and the Reference Mass 2, 
          for example m/z = 331 and m/z = 381 for the reference compound PFK: <br/>

          •  Either enter the masses directly in the provided input boxes. <br/>
          •  Or click the <b>Browse</b> button to open the Reference And Exception Masses dialog 
          and drag the masses into the input boxes. <br/>

          <b>Electric area with parameters for manual electric calibration</b>  <br/>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_38.png`} width="auto"  alt=''></img><br/>

          See Using Masses from the Reference and Exception Masses Dialog for advice 
          on using the Reference And Exception Masses dialog.<br/>
          5.  Click <b>Calibrate</b> to start the electric calibration. <br/>

          The software will establish an electric calibration with the information 
          from the Reference Mass 1 and Reference Mass 2.<br/>

          <b>Graph View showing the graph of an electric calibration</b>  <br/>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_40.png`} width="auto"  alt=''></img><br/>

          In the Instrument Status View, the parameters that describe the electric calibration are displayed:<br/>
          •  EDAC zero and EDAC gain describe the calibration. <br/>
          •  ECORR is the correction factor applied to the accelerating voltage to correct 
          for a slight drift in the magnet calibration.  <br/>

          After a successful calibration, the EDAC Correction Parameters normally differ 
          from the default values.<br/>

          <b>EDAC Corr Parameter Group</b>  <br/>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_41.png`} width="auto"  alt=''></img><br/>

          The electric calibration is valid until a new electric calibration is performed. 
          </p>
          
          <hr className='class-3'/>
          <h5 id="content315">3.1.5 Reference Tables</h5>
          <a href='#content0'>To Contents</a><br/>
          <p>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Reference Tables<br/>
          The DFS SmartTune software is supplied with several reference tables, 
          which contain information on the reference masses, etc. of the selected compound. 
          During the (magnetic and electric) calibration, the software will use this information 
          to set the instrument to the correct masses.<br/>
          In addition, you can set up new reference tables according to the analysis requirements. 
          Therefore, you may use any sample as reference compound when you work with the DFS mass spectrometer.<br/>
          The reference tables supplied with the instrument can be edited 
          in the Reference And Exception Masses dialog of the DFS SmartTune software.<br/>
          
          <b>Tip</b>  In addition to the magnet calibration and the electric calibration, 
          also the peak matching function reads the selected reference information. <br/>

          <b>Editing Existing Reference Tables</b><br/>
          
          <b>*  To edit the reference tables supplied with the instrument</b> <br/>

          1.  Click the <b>Calibrate</b> button to open the Calibrate dialog box. <br/>
          2.  Click the <b>Advanced</b> tab to open the Advance page. <br/>
          3.  Click <b>Edit</b> to open the Reference And Exception Masses dialog. <br/>
          4.  Load the reference table into the dialog: <br/>

          a.  Choose <b>File &gt; Open</b> and browse for the respective file in the Import Reference Table dialog box. <br/>
          b.  Select the reference mass table and click <b>Open</b> to load the table into the 
          Reference And Exception Masses dialog. <br/>

          5.  In the Reference And Exception Masses dialog, 
          double-click the reference mass to be edited to open the Edit Reference Mass dialog box. <br/>

          <b>Tip</b>  A similar dialog box exists for editing exception masses. <br/>

          6.  Change the mass or intensity and click <b>Add</b> to enter the revised mass in the table. <br/>
          7.  Repeat steps 5 to 6 until the reference table meets your requirements.  <br/>
          8.  Choose <b>File &gt; Save</b> As and save the reference table under a new name.  <br/>

          The new reference table is now added to the list of available files and can be selected 
          for example for calibration.<br/>
          
          <b>Creating New Reference Tables</b><br/>          
          
          <b>*  To create a new reference table</b> <br/>

          1.  In the Reference And Exception Masses dialog, choose <b>File &gt; New</b> 
          to open an empty reference table. <br/>
          2.  Click <b>New</b> to open the New Reference or Exception Mass dialog box. <br/>
          3.  Type in Mass, Intensity, and Formula of the new list item, for example 
          Mass = 4.002055, Intensity = 100, and Formula = He+ (for FC43). <br/>

          <b>Tip</b>  The Reference And Exception Masses dialog does not support 
          fractional numbers as intensities. Therefore, we recommend to multiply 
          any percentage number by 100 and use only the integer values (for example, 
          2.74% would give an intensity of 274). <br/>

          4.  In the Peak type area, select the appropriate option button. <br/>
          5.  Click <b>Add</b> to enter the new mass in the table. <br/>
          6.  Repeat steps 3 to 5 until all masses have been added to the table.  <br/>
          7.  Choose <b>File &gt; Save</b> As and save the reference table under a new name.  <br/>

          The new reference table is now added to the list of available files 
          and can be selected for example for calibration.<br/>
          
          <b>Using Masses from the Reference and Exception Masses Dialog</b><br/>
          
          Use the Reference and Exception Masses dialog to enter exact masses 
          of reference and exception peaks into input fields on various dialog boxes 
          in DFS SmartTune (for example, the MID dialog box).<br/>
          
          <b>*  To add a mass to the table on the MID dialog box</b> <br/>

          1.  Right-click in the MID mass table to open the shortcut menu. <br/>
          2.  Choose <b>Masses Dialog</b> to open the Reference and Exception Masses dialog. <br/>
          3.  Choose <b>File &gt; Open</b> and import the reference file (pbrdpe_mw.dref, for example). 
          The various reference masses will be displayed. <br/>
          4.  Drag the mass, in our example 405.8021 (Tri-BDPE), to an empty mass space 
          in the MID mass table. <br/>

          <b>Dragging a reference mass into a dialog box</b>  <br/>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_44.png`} width="auto"  alt=''></img><br/>

          5.  To close the Reference and Masses dialog, click <b>Close</b>. <br/>

          </p>
			  
        <hr className='class-2'/>
        <h4 id="content032">3.2 Multiplier Gain Determination</h4>
        <a href='#content0'>To Contents</a><br/>
          <p>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Multiplier Gain Determination<br/>
          The multiplier gain of the DFS mass spectrometer is calibrated with the 
          Calibrate dialog box of the DFS SmartTune software and can be carried out with 
          a semi-automatic procedure.<br/>
          
          To ensure reliable operation of you mass spectrometer, it is important that you 
          have the same multiplier gain for all your measurements. Thermo Fisher Scientific 
          recommends having a multiplier gain of 106 adjusted for most of the measurement. 
          This gives the most reliable results. For dioxin measurements, a multiplier gain of 2×106 is used.<br/>
          Tip  Because multipliers age, their gain at constant multiplier voltage decreases. 
          Therefore, it is mandatory to check the multiplier gain at regular intervals, 
          at least every two months. <br/>

          <b>Calibrating the Multiplier Gain</b><br/>

          The multiplier gain calibration can be carried out by starting the multiplier 
          gain calibration procedure on the semi-automatic page.<br/>
          
          <b>Semi-Automatic Calibration</b><br/>
          <b>*  To perform a semi-automatic calibration of the multiplier gain</b> <br/>

          1.  Click the <b>Calibration</b> button in the toolbar or choose <b>Control &gt; Calibrate</b> to 
          open the Calibrate dialog box of the DFS SmartTune software. <br/>
          2.  Click the <b>Semi-Automatic</b> tab to display the Semi-Automatic page. <br/>
          3.  Click the <b>Start</b> button next to the Multiplier gain item to start the calibration 
          of the multiplier gain. <br/>

          <b>Calibrate dialog box—Multiplier gain calibration</b>  <br/>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_46.png`} width="auto"  alt=''></img><br/>

          The progress of the calibration is indicated in the Status box. When completed, 
          the result of the calibration is indicated in the result column (✓= successful, ✗= failed).<br/>
          
          <b>Calibration Failed</b><br/>

          If the calibration has failed, check the following items:<br/>
          •  Multiplier voltage <br/>

          •  Conversion dynode status <br/>

          <b>*  To check the detection settings</b> <br/>

          Open the Detection Settings dialog box by clicking the <b>Detection Settings</b> button 
          in the Instrument Control bar or by choosing <b>Setup &gt; Detection</b>.<br/>

          </p>
			  
        <hr className='class-2'/>
        <h4 id="content033">3.3 Resolution Check Procedure</h4>
        <a href='#content0'>To Contents</a><br/>
          <p>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img><br/>
          Resolution Check Procedure<br/>
          With the Resolution Check Procedure it is possible to create a PDF Quality Report 
          for the reference masses according to the EPA Method 1613. The report settings 
          are implemented in the instrument method of the DFS mass spectrometer.<br/>

          <b>Resolution Check Functions</b><br/>

          <b>Tip</b>  When the resolution script is activated, the procedure will start before 
          the GC goes in <b>Ready to Inject</b>. While the script is running, the status of the devices, 
          such as GCs, are <b>Preparing to Run</b> whereas the status of the DFS remains <b>Downloading</b> 
          in the Xcalibur Status view. The procedure takes some minutes depending on the number of MID sections. <br/>

          The report will be saved in a folder generated according to the data path. 
          Report and folder have the same name as the raw file.<br/>

          <b>Activate</b><br/>

          To activate the script, select the <b>Activate</b> check box.<br/>

          <b>Report Reference</b><br/>

          In addition to the used reference masses, as lock and cali mass, 
          all reference masses within the mass range of each window are reported. 
          So far, the reference mass tables fc43 and fc43+ have been implemented.<br/>

          <b>Target Resolution</b><br/>

          The target resolution value is displayed in the header of the PDF report. 
          The value has no other function.<br/>

          <b>Target resolution within DFS Resolution Check Report</b>  <br/>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_47.png`} width="auto"  alt=''></img><br/>

          <b>Resolution Validation</b><br/>

          This topic describes the parameters of the Resolution Validation area.<br/>

          <b>Warning if Below</b><br/>
          
          Use this field to enter a minimum resolution. If the resolution of 
          a lock or cali mass drops below this value, a Warning message appears 
          in the Status line of the report header. If the resolutions of all lock 
          and cali masses exceed this value, the message <b>Resolution passed</b> appears.<br/>

          <b>Error if Below</b><br/>
          
          Use this field to enter a minimum resolution. If the resolution of 
          a lock or cali mass drops below this value, the message <b>Resolution failed</b> 
          appears in the Status line of the report header.<br/>

          <b>Stop Sequence on Error</b><br/>

          If the resolution of a lock or cali mass drops below the value 
          in the Error if below field, the sequence is halted. The message <b>Resolution 
          failed - Sequence stopped</b> appears in the report.<br/>

          <b>Resolution failed - Sequence stopped</b>  <br/>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_49.png`} width="auto"  alt=''></img><br/>


          When a sequence is stopped by the script, the message !!!Resolution out of range. 
          Sequence halted! appears in the DC tab of the DFS SmartTune software.<br/>

          <b>Resolution out of range - Sequence halted</b>  <br/>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_48.png`} width="auto"  alt=''></img><br/>

          </p>
          
          <hr className='class-3'/>
          <h5 id="content331">3.3.1 Report</h5>
          <a href='#content0'>To Contents</a><br/>
            <p>
            <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Report<br/>
            <br/>
            This topic describes the content of a quality report.<br/>

            <b>Report Logo</b><br/>

            Per default, a Thermo Fisher Scientific logo appears in the upper right corner of the PDF report.<br/>

            <b>Default of report logo</b>  <br/>
            <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_52.png`} width="auto"  alt=''></img><br/>

            You can replace the default logo with your own company logo. To do so, overwrite the file named ResCheckLogo.jpg, which is located at 
            C:\Xcalibur\systems\DFS\pictures.<br/>

            <b>Lock and Cali Masses</b><br/>

            For each MID section, a tune graphic of the lock and cali mass is displayed. The resolution is calculated, and the mass deviation of the peak is displayed in ppm in the peak graphic.
            
            <b>Tune graphics of lock and cali mass</b> <br/>
            <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_51.png`} width="auto"  alt=''></img><br/>


            </p>

		</div>


  )


}
