						<tr>
							<td></td>
							<td>Save Spreadsheet Layout to</td>
							<td>Choose this command to open the Set Layout Name dialog box. Here,  you can save the current layout under a new name. The new layout is  displayed as new entry under <b>Layouts</b>. See “Set Layout Name Dialog  Box” on page 4-43 for details.  <br/>For instructions about deleting spreadsheet layouts, see page 8-59. </td>
						</tr>
