import React from 'react';

function DSF_0() {
  return (
    <div className="px-4" style={{marginTop:30}}>
      <MainContents />
    </div>
  );
};

export default DSF_0;

function MainContents() {
	const title = 'DSF Operating';

	return (
    <>
      <h1 className='title'>{title}</h1>	
      <hr className='class-1'/>
      <img src={`${process.env.PUBLIC_URL}/images/operating/ch2_1.jpg`} height='auto' alt=''></img><br/>
    </>
  )
}
