import React from 'react';

function DFS_Help_1() {
	return (
		<>
			<div className="px-4" style={{marginTop:30}}>
					<MainContents />
			</div>
		</>
	);
};
export default DFS_Help_1
function MainContents() {
	const title = 'Welcome to the DFS Help!';

	return (
		<div>
			<h1 className='title' id="content0">{title}</h1>	
			<hr className='class-1'/>
      <h5>Contents</h5>
      <a href='#content01'>1. Welcome to the DFS Help!</a><br/>
      <a href='#content11'>1.1 Using this Help</a><br/>
      <a href='#content12'>1.2 Using Help Buttons and Menus</a><br/>
      <a href='#content13'>1.3 Printing Help</a><br/>
      <a href='#content14'>1.4 Contacting Us</a><br/>
      <a href='#content15'>1.5 Thermo Scientific POPs Center of Excellence</a><br/>
			<hr className='class-1'/>

      <h2 id="content1">1. Welcome to the DFS Help!</h2>
        <p>
        <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Welcome to the DFS Help!<br/>
        This Help provides reference information about the parameters 
        in the Tune window. For information about the operating procedures 
        for the DFS mass spectrometer, we recommend that you read the DFS 
        Operating Manual in its entirety.<br/>
        Helpful Links<br/>
        •  Contacting Us <br/>
        •  Getting Started <br/>
        •  Explore DFS SmartTune <br/>
        Accessing other DFS Documents<br/>
        In addition to this Help, Thermo Fisher Scientific provides the following 
        documents for the DFS mass spectrometer:<br/>
        •  DFS Pre-Installation Requirements Guide <br/>
        •  DFS Operating Manual <br/>
        •  DFS Software Manual <br/>
        •  DFS Consumables and Parts Catalog <br/>
        You can access PDF files of the documents listed above from the data system computer.<br/>
        <b>*  To view product manuals</b> <br/>
        Go to <b>Start &gt; All Programs &gt; Thermo DFS &gt; Manuals</b>.<br/>
        <b>*  To open Help</b> <br/>
        •  From the Tune window, choose <b>Help &gt; DFS Tune Help</b>. <br/>
        •  If available for a specific window or dialog box, click <b>Help</b> or 
        press <b>F1</b> for information about setting parameters. <br/>
        For more information, including upcoming application notes, 
        visit www.thermofisher.com.<br/>

        </p>

			  <hr className='class-2'/>
        <h4 id="content11">1.1 Using this Help</h4>
        <a href='#content0'>To Contents</a><br/>
          <p>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Using this Help<br/>
          Click the left and right arrows (
            <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_350.png`} width="auto"  alt=''></img>
            <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_349.png`} width="auto"  alt=''></img>
          ) on the Help window to move to a defined next or previous topic. The arrows provide a complete path to explore the Help. <br/>
          To return to a previous screen, click .<br/>
          View help button and menu command functions, as well as print features:<br/>
          &nbsp;&nbsp;•  Using Help Buttons and Menus <br/>
          &nbsp;&nbsp;•  Printing Help <br/>
          <b>*  To find a specific topic on the Contents page</b> <br/>
          1.  Click the <b>Contents</b> tab to display the table of contents for Help.  <br/>
          2.  To display a Help topic: <br/>
          &nbsp;&nbsp;•  Double-click an entry to display its Help topic. <br/>
          &nbsp;&nbsp;•  Double-click <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_351.png`} width="auto"  alt=''></img> to display subtopics. <br/>
          <b> *  To find a specific topic on the Index page</b> <br/>
          1.  Click the <b>Index</b> tab to display the Index page for the Help. <br/>
          2.  In the box at the top of the page, type the first few letters of a topic.  <br/>
          The list scrolls automatically to the relevant area of the index and highlights the most likely index entry.<br/>
          3.  Click the index entry.  <br/>
          &nbsp;&nbsp;If the entry is associated with a single topic, that topic appears in the Help window. If the entry is associated with two or more topics, a pop-up menu appears. <br/>
          4.  To view the topic of interest, select it from the list. <br/>
          <b>*  To find a specific topic using the Search page</b> <br/>
          1.  If you have already created a word list, go to step 4. <br/>
          2.  Click the <b>Search</b> tab to display the Search page for the Help.  <br/>
          &nbsp;&nbsp;The Find Setup wizard appears the first time you open the Search page. This feature builds a word list from all of the words in all of the Help topics. <br/>
          3.  Click <b>Next</b> and <b>Finish</b> to build the word list. <br/>
          4.  In the box at the top of the Search page, type the words you want to find. <br/>
          5.  Click <b>List topics</b>. <br/>
          6.  Double-click the topic of interest. The topic appears in the Help window. <br/>
          </p>

			  <hr className='class-2'/>
        <h4 id="content12">1.2 Using Help Buttons and Menus</h4>
        <a href='#content0'>To Contents</a><br/>
          <p>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>
          Using Help Buttons and Menus<br/>
          This topic describes print, navigational aids, and display options for the Help. <br/>
          <b>Help Buttons</b><br/>
          The Help contains these buttons:<br/>


          <table className='table  table-bordered table-striped'>
            <tbody>
              <tr className='table-info'>
                <td>Feature</td>
                <td>Description</td>
              </tr>
              <tr>
                <td><img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_353.png`} width="auto"  alt=''></img></td>
                <td>Hides the side panel containing the Contents, Index, and Search tabs. 
                  This button changes to Show when the side panel is hidden.</td>
              </tr>
              <tr>
              <td><img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_354.png`} width="auto"  alt=''></img></td>
                <td>Returns to the previously displayed Help topic. 
                  The Back button is available only if you have previously displayed a topic.</td>
              </tr>
              <tr>
              <td><img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_355.png`} width="auto"  alt=''></img></td>
                <td>Displays the Print dialog box so that you can print the information displayed in the current Help topic.</td>
              </tr>
              <tr>
              <td><img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_356.png`} width="auto"  alt=''></img></td>
                <td>Displays the Help Options menu with the following commands:<br/>
                Hide Tabs: Hides the Help tabs, the Contents list, the Index, and the Search facility. 
                Click Show Tabs to display these items.<br/>
                Back/Forward: Moves to the previously viewed page or to the next page in the Help.<br/>
                Home: Opens the Welcome page.<br/>
                Stop: Stops the current action.<br/>
                Refresh: Refreshes the Help page.<br/>
                Internet Options: Displays the Internet Options dialog box where you can specify the look, 
                security, privacy, and other Internet features.<br/>
                Print: Prints the Help topic or specified pages of the Help topic to your local printer.<br/>
                Search Highlight On/Off: Turns on or off the highlight that appears on 
                every instance of a found keyword after a search.</td>
              </tr>
              <tr>
              <td><img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_350.png`} width="auto"  alt=''></img></td>
                <td>Displays the previous topic in the predefined browse sequence, 
                  based on the order of topics in the Contents page.</td>
              </tr>
              <tr>
              <td><img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_349.png`} width="auto"  alt=''></img></td>
                <td>Displays the next topic in the predefined browse sequence, 
                  based on the order of topics in the Contents page.</td>
              </tr>
              <tr>
              <td><img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_357.png`} width="auto"  alt=''></img></td>
                <td>Expands the content of the current Help topic to display additional information.</td>
              </tr>
              <tr>
              <td><img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_358.png`} width="auto"  alt=''></img></td>
                <td>Hides collapsible content of the current Help topic.</td>
              </tr>
              <tr>
              <td><img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_359.png`} width="auto"  alt=''></img></td>
                <td>Displays a list of topics that may be of interest to users viewing the current Help topic.</td>
              </tr>
            </tbody>
          </table>


          <b>Help Menu Options</b><br/>
          The Help menu in the Tune window provides these options:<br/>
          <table className='table  table-bordered table-striped'>
            <tbody>
              <tr className='table-info'>
                <td>Help</td>
                <td>Description</td>
              </tr>
              <tr>
                <td>Help Overview</td>
                <td>Displays the Help.</td>
              </tr>
              <tr>
                <td>Help Content</td>
                <td>Displays the table of contents for the Help.</td>
              </tr>
              <tr>
                <td>Help Index</td>
                <td>Displays the index for the Help.</td>
              </tr>
              <tr>
                <td>About</td>
                <td>Displays the About dialog box with information about the instrument and the current DFS SmartTune version</td>
              </tr>
            </tbody>
          </table>
          </p>

			  <hr className='class-2'/>
        <h4 id="content13">1.3 Printing Help</h4>
        <a href='#content0'>To Contents</a><br/>
          <p>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Printing Help<br/>
          You can print information in the Help topics and in the pop-up windows.<br/>
          <b>*  To print a specific topic</b> <br/>
          1.  Display the topic you want to print. <br/>
          2.  Click <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_355.png`} width="auto"  alt=''></img> on the toolbar to open the Print dialog box. <br/>
          3.  Enter print settings and click <b>OK</b>. <br/>
          <b>*  To print a pop-up window</b> <br/>
          1.  Right-click the pop-up window you want to print. <br/>
          2.  Choose <b>Print</b> from the shortcut menu. <br/>
          </p>

			  <hr className='class-2'/>
        <h4 id="content14">1.4 Contacting Us</h4>
        <a href='#content0'>To Contents</a><br/>
          <p>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Contacting Us<br/>
          There are several ways to contact Thermo Fisher Scientific. You can use your smartphone to scan a QR Code, which opens your email application or browser.<br/>
          Contact Link / Remarks QR Code <br/>
          Brochures and Ordering Information www.thermofisher.com/dfs  <br/>
          Service Contact www.unitylabservices.com  <br/>
          Technical Documentation SharePoint *  To get user manuals for your product <br/>
          1.  With the serial number (S/N) of your instrument, request access on our customer SharePoint as a customer at www.thermoscientific.com/Technicaldocumentation <br/>
          2.  For the first login, you have to create an account. Follow the instructions given on screen. Accept the invitation within six days and log in with your created Microsoft™ password. <br/>
          3.  Download current revisions of user manuals and other customer-oriented documents for your product. Translations into other languages may be available there as well. <br/>
          <br/>
          Customer Feedback *  To suggest changes to this Help <br/>
          You are encouraged to report errors or omissions in the Help. Send an email to the Technical Documentation at documentation.bremen@thermofisher.com.<br/>
          The PDF versions of our manuals allow adding comments with Adobe Acrobat Reader or other freely available PDF reader programs. <br/>
          </p>

			  <hr className='class-2'/>
        <h4 id="content15">1.5 Thermo Scientific POPs Center of Excellence</h4>
        <a href='#content0'>To Contents</a><br/>
          <p>
          <img src={`${process.env.PUBLIC_URL}/images/DFS_Help/img_352.png`} width="auto"  alt=''></img>Thermo Scientific POPs Center of Excellence<br/>
          To support laboratories charged with the task of researching and monitoring POPs, Thermo Fisher Scientific has established the POPs Center of Excellence. This direct resource for our customers and associates is staffed with experts in POPs analysis. Our commitment ensures high productivity, added-value-solutions for some of the most challenging POPs determinations.<br/>
          Located in Bremen, Germany, the POPs Center of Excellence will look to support our international customers and associates, addressing a wide range of requirements.<br/>
          •  Reference for POPs analysis <br/>
          •  Point for access to POPs methodology on Thermo Scientific products <br/>
          •  Resource for collaborative projects on the latest instrumentation <br/>
          •  Knowledge hub for Thermo Scientific product users who are working in POPs analysis <br/>
          •  Partner to industrial and governmental laboratories; improving quality and efficiency in our world-wide POPs monitoring capacity  <br/>
          For more information about the integrated POPs workflow, visit<br/>
          www.thermofisher.com/POPs<br/>
          </p>

		</div>


  )


}
