import React from 'react';

function DFS_OM_en_1() {
	return (
		<>
			<div className="px-4" style={{marginTop:30}}>
					<MainContents />
			</div>
		</>
	);
};
export default DFS_OM_en_1
function MainContents() {
	const title = 'Chapter 1. Using this Manual';


	return (
		<div>
			<h1 className='title'>{title}</h1>	

			<hr className='class-1'/>
			<h2>Technical Data for DFS Systems</h2>
				<p>The table summarizes the most important technical data of the DFS Magnetic Sector GC-HRMS system. See the respective chapters of the manual for details and additional instrument properties.</p>

			<hr className='class-1'/>
			<h2>1. Using this Manual</h2>
			<h2>2. About this Manual</h2>
			<h2>3. Typographical Conventions </h2>
				<h4>3.1 Signal Words </h4>
				<h4>3.2 Viewpoint Orientation</h4>
				<h4>3.3 Data Input</h4>
				<h4>3.4 Topic Headings</h4>
				<h2>4. Reference Documentation</h2>
				<h2>5. Contacting Us</h2>
				<h2>6. Thermo Scientific POPs Center of Excellence</h2>
		</div>


  )
}