import React from 'react';


function TargetQuan_2() {
	return (
		<>
			<div className="px-4" style={{marginTop:30}}>
					<MainContents />
			</div>
		</>
	);
};
export default TargetQuan_2
function MainContents() {
	const title = 'Chapter 2 Introduction to Quantification';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			<h2>1. Introduction to Quantification</h2>	
			<hr className='class-1'/>
				<p>This chapter explains some of the basic principles and terminology of  quantification.</p>
	
			<hr className='class-1'/>
			<h2>2. About Quantitative Analysis</h2>	
				<p>
				Quantitative analysis is the process of measuring the amount of a  particular component in a sample. 
				In some cases, such as in trace  analysis, you may only want to estimate the quantity of a component. 
				It  may be sufficient to know that the component is present at a level  significantly higher or lower than a defined threshold. 
				For example, it is  not generally important to know whether a patient has overdosed 15 or  20 times above a prescribed limit, 
				but simply that the limit has been  exceeded. In such cases, a rapid measurement is required rather than a  precise one. 
				This form of measurement is generally called semi  quantitative analysis. In other applications, a dioxin measurement for  example, 
				you may be seeking the maximum possible accuracy from your  measurements. 
				Time and cost of analysis are less important than  achieving the highest possible standards in precision and accuracy.  
				</p>

			<hr className='class-1'/>
			<h2>3. Quantification</h2>	
				<p>
				To carry out quantification, you need to determine the instrument’s  response to known amounts of the target component. 
				Response is based  on either the height of the chromatogram peak, or more commonly, the  area under the peak’s profile. 
				Both cases take account of the detected  peak’s baseline. Instrument response is generally measured with several  
				samples commonly called standards, or calibration standards. These  should represent a suitable wide range of concentrations or amounts.  
				Responses to these standards are plotted in a graph called the calibration  curve. 
				This usually reveals an essentially linear relationship between  amount and response, 
				although more complex relationships are  occasionally observed. TargetQuan fits an equation to the calibration  
				curve by a user-chosen method (for example, a linear regression). 
				This  provides a Response factor—a comparative measure of the response of the  mass spectrometer to a component. 
				It is based on the amount of sample  injected and the resulting peak area or peak height. Thus, the response  
				factor reflects how responsive or sensitive the mass spectrometer is to a  certain component. 
				The precision of the obtained response factor  depends on the quality (and to a lesser extent on the quantity) of the  calibration data.
				</p>


			<hr className='class-1'/>
			<h2>4. Quantification Techniques </h2>
				<p>Introduction to Quantification  Quantification Techniques  Quantification Techniques  </p>
				<ul>
					<li>External standard quantification </li>
					<li>Internal standard quantification </li>
				</ul>
				<p>The chosen method determines the manner in which response is  calculated, 
					both for the generation of calibration curves and for  subsequent quantification.  </p>

			
				<hr className='class-2'/>
				<h4>4.1 External Standard Quantification</h4>
				<p>The external standard quantification technique utilizes a component’s  absolute peak area or height directly as its response factor. 
					The response  factor is then used to refine a calibration curve (from standard samples),  or to compute an amount (for unknown samples) 
					using the calibration  curve. The external standard quantification approach offers time and  
					cost effective quantification although it is not capable of achieving the  very highest precision and accuracy. 
					For semi-quantitative analysis, 
					a  single external standard sample may provide sufficient calibration  information to achieve the required accuracy. </p>


				<hr className='class-2'/>
				<h4>4.2 Internal Standard (ISTD) Quantification</h4>
					<p>
					An Internal Standard (ISTD) is a component added to a sample to act as  a response reference for one or more non-ISTD components in the  sample. 
					The concentration or amount of an ISTD in any standard or  unknown sample remains constant. 
					Since the ISTD and non-ISTD  components are analyzed together, 
					the internal standard quantification  approach has the advantage that it corrects for injection and other  sample handling errors. 
					Ideally, an ISTD should closely be related to the  target component in terms of both its physical and chemical properties.  
					Typically, ISTD components are analogs, homologues or isomers of the  target non-ISTD component. An ideal ISTD is a structural or  
					isotopically labeled analog of one of the target components. The internal  standard quantification technique computes response values based on  
					the ratio of a component’s absolute peak area or height to that of the  ISTD. 
					This provides a peak area ratio. The peak area ratio value is then  used to form a calibration curve (from standard samples), 
					or to calculate  an amount (for unknown samples) using the calibration curve. 
					There  can be any number of ISTD components in a sample but each  non-ISTD component can only be calibrated against an ISTD.
					</p>

				<hr className='class-2'/>
				<h4>4.3 Isotope Dilution Technique</h4>
					<p>
					An internal standard quantification using isotopically labeled standards  is called isotope dilution quantification. An isotope dilution standard is  
					a synthetic isomer of a target compound with one or more atoms  replaced by heavier or lighter isotopes of the same element. 
					As the  chemical structure of the native compound and its labeled analog are  identical they exhibit similar effects during sample preparation.  
					Therefore, a very accurate quantification of the target compounds is  achieved.  </p>
					<p>To compensate variability during clean up and chromatographic  separation, 
						standards can be added to the sample at different points of  the work-up. (See <b>Figure</b> 2-1.)  <br/>
						<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_06.jpg`} width="auto"  alt=''></img><br/>
						<b>Figure</b> 2-1. Adding standards 
					</p>

					<p>
					Standard 1 is added before taking the sample. Prior to the clean-up,  standard 2 is added. 
					Standard 3 is added as very last step before  quantification. Throughout this manual, we will use the following  nomenclature:  
					</p>
					<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_07.jpg`} width="auto"  alt=''></img><br/>

					<p>
					<b>NOTICE : </b> Numbers and nomenclature of the standards depend on the  quantification method in use and can vary from laboratory to  laboratory.
					</p>
					<p><b>Figure</b> 2-2 shows the standards hierarchy as it is displayed by  TargetQuan in the Entries and Dependencies view. </p>
					<br/>
					<img src={`${process.env.PUBLIC_URL}/images/targetquan/img_39.png`} width="auto"  alt=''></img><br/>
					<b>Figure</b> 2-2. Hierarchy of standards for isotope dilution technique 




			<hr className='class-1'/>
			<h2>5. Quantification Software </h2>
				<p>
				Thermo Fisher Scientific offers two software packages for the  quantitative evaluation of your raw files, the Quan Browser—as part of  Xcalibur—and TargetQuan.  </p>
				<p><b>NOTICE : </b> You can install and operate TargetQuan without having  Xcalibur installed on your computer. 
				</p>

			
				<hr className='class-2'/>
				<h4>5.1 Quan Browser </h4>
				<p>
				The Quan Browser covers external standard quantification methods as  well as internal standard methods using only one type of ISTD. 
				For how  to use the Quan Browser, refer to the Getting Productive manual and the  Help.
				</p>
				<p>
					<b>NOTICE : </b> Quan Browser—the basic quantitation program under  Xcalibur—does not support the usage of the isotope dilution  technique.
				</p>


				<hr className='class-2'/>
				<h4>5.2 TargetQuan </h4>
					<p>
					The target compound quantification program TargetQuan has been  designed for the evaluation of stable isotope dilution measurements.  
					TargetQuan lets you set up a quantitation file (quan file) of target  
					compounds and standards including the retention time, isotope dilution  masses for each compound. 
					</p>
					<p>
					TargetQuan identifies the different compounds by the help of the  specific masses, specified retention times, 
					and the ratios of quan and  ratio masses. First, it measures the area of the peak corresponding to the  peak detection parameters. 
					Then, TargetQuan uses the response factor  for the compound to calculate the amount of the compound present in  the sample. 
					</p>
					<p>
						Different types of ISTD can be defined. TargetQuan should especially  be used for quantitative analysis of high-resolution data. TargetQuan  helps you to: 
					</p>
					<ul>
						<li>
							Generate new quantification methods including:  <br/>
							-  Setup of target compound and ISTD dependences<br/>
							-  Identification of target compounds and ISTD by retention time,  quantification mass and isotope ratio  <br/>
							-  Peak detection parameters for the evaluation of peak areas  <br/>
							-  Specification of target compound and ISTD amounts for  response factor calculation  
						</li>
						<li>Modify existing quantification methods (for example, the Dioxin  methods EPA 1613 and EN 1948) </li>
						<li>Automatically process raw files </li>
						<li>Review and recalculate quantification results  </li>
						<li>Report quantification results </li>
					</ul>
					<p>
						TargetQuan displays the results of the quantification in predefined  spreadsheet layouts. For a description of the predefined layouts, 
						see  Chapter 10: “Spreadsheet Layouts”. For electronic distribution of the  quantification results, TargetQuan can create report documents. 
						You  can modify the format of these reports in any way that meets your  
						specific needs. For instructions on modifying report documents and  spreadsheet layouts, see Chapter 8: “Procedures in TargetQuan”.
					</p>

					<h6>Principle of Quantification </h6>
						<p>The quantification is always a two-step process: </p>
						<ol>
							<li>Calibration: Calculation of relative response factors (= response  curve)  <br/><br/>
							For this purpose, data files corresponding to calibration solutions  (for example, CS1 to CS5 calibration solutions for EPA 1613)  
							containing well-known amounts of the different native target  compounds have to be used. </li>
							<li>Quantification of unknown compounds: Quantifying the unknown  amounts of the native target compounds of interest in a sample  
								extract using the relative response factors determined in step 1.</li>
							
						</ol>

						<h6>Quan File Types  </h6>
						<p>TargetQuan saves all parameters as well as calculated values in so-called  quan files (*.quan). 
							In practice two types of quan files can be  distinguished: </p>
						<ul>
							<li>Default quan files are the files you have to set up according to a  specific application and analysis method before starting the  
								integration/calculation process. These files contain all parameters  needed to find and integrate the specific compound peaks in an  Xcalibur data file. 
								Thy also contain the parameters needed for  correctly calculating amounts or response factors (for example,  
								retention times, exact masses for the different mass traces, specified  amounts of internal standards).   <br/><br/>
								You can use default files whenever you run the same application  using the same analytical setup. 
							</li>
							<li>
								Result quan files are the type of files you get after the  integration/calculation process. 
								In addition to the parameters stored  in the corresponding default quan file, these files contain the results  
								of the integration/calculation (for example, peak areas, relative  response factors, calculated amounts). 
								TargetQuan creates result files  automatically and stores them within the quan sequence file. 
							</li>
						</ul>
    </div>


  )
}
